// src/components/common/FileSelector.js

import React, { useState, useRef, useEffect } from 'react';
import { FolderOpen, Play, Square, Image, Music, X } from 'lucide-react';

const FileSelector = ({
  value,
  onChange,
  placeholder,
  onFileSelect,
  acceptTypes,
  icon,
  assets,
  showDirectUpload = false // 直接アップロードボタンを表示するかどうか
}) => {
  const [showAssetPicker, setShowAssetPicker] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [displayName, setDisplayName] = useState('');
  const audioRef = useRef(null);
  const pickerRef = useRef(null);
  
  // アセットタイプを判断
  const getAssetCategories = () => {
    if (acceptTypes.includes('image')) {
      return ['image/bg', 'image/character'];
    } else if (acceptTypes.includes('audio')) {
      return ['sound/bgm', 'sound/se'];
    }
    return [];
  };
  
  // クリック外部検出
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (pickerRef.current && !pickerRef.current.contains(event.target)) {
        setShowAssetPicker(false);
      }
    };
    
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);
  
  // 音声プレビュー
  const toggleAudioPreview = (audioUrl) => {
    if (!audioUrl) return;
    
    if (isPlaying) {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current.currentTime = 0;
      }
      setIsPlaying(false);
    } else {
      if (!audioRef.current) {
        audioRef.current = new Audio(audioUrl);
        audioRef.current.onended = () => setIsPlaying(false);
      } else {
        audioRef.current.src = audioUrl;
      }
      
      audioRef.current.play()
        .then(() => setIsPlaying(true))
        .catch(err => {
          console.error('音声再生エラー:', err);
          setIsPlaying(false);
        });
    }
  };
  
  // クリーンアップ
  useEffect(() => {
    return () => {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current.src = '';
      }
    };
  }, []);
  
  // BlobURLからファイル名を取得
  useEffect(() => {
    if (value && value.startsWith('blob:') && assets) {
      // アセットからファイル名を検索
      let foundFileName = '';
      const categories = getAssetCategories();
      
      for (const category of categories) {
        const [mainCategory, subCategory] = category.split('/');
        if (assets[mainCategory] && assets[mainCategory][subCategory]) {
          const files = assets[mainCategory][subCategory];
          for (const fileName in files) {
            if (files[fileName].url === value) {
              foundFileName = fileName;
              break;
            }
          }
          if (foundFileName) break;
        }
      }
      
      setDisplayName(foundFileName || 'ファイルが選択されています');
    } else {
      setDisplayName('');
    }
  }, [value, assets]);
  
  // アセットの有効性確認
  const hasValidAssets = () => {
    const categories = getAssetCategories();
    for (const category of categories) {
      const [mainCategory, subCategory] = category.split('/');
      if (assets && 
          assets[mainCategory] && 
          assets[mainCategory][subCategory] &&
          Object.keys(assets[mainCategory][subCategory]).length > 0) {
        return true;
      }
    }
    return false;
  };
  
  // 値をクリア
  const clearValue = () => {
    onChange('');
    if (isPlaying && audioRef.current) {
      audioRef.current.pause();
      audioRef.current.currentTime = 0;
      setIsPlaying(false);
    }
  };
  
  const isAudio = acceptTypes.includes('audio');
  
  return (
    <div className="relative">
      <div className="flex">
        <div className="relative flex-1">
          <input
            type="text"
            value={displayName || value || ''}
            onChange={(e) => {}} // 直接編集は不可
            placeholder={placeholder}
            className="w-full p-2 border rounded-l"
            readOnly={true} // 読み取り専用に
          />
          {/* クリアボタン - 値がある場合のみ表示 */}
          {value && (
            <button
              type="button"
              onClick={clearValue}
              className="absolute right-2 top-1/2 transform -translate-y-1/2 text-gray-500 hover:text-gray-700"
              title="クリア"
            >
              <X size={16} />
            </button>
          )}
        </div>
        
        {/* 直接ファイル選択（オプション） */}
        {showDirectUpload && (
          <label className="px-3 py-2 bg-blue-500 text-white hover:bg-blue-600 cursor-pointer flex items-center">
            {icon || <FolderOpen size={16} />}
            <input 
              type="file" 
              className="hidden" 
              accept={acceptTypes}
              onChange={(e) => {
                if (e.target.files[0]) {
                  onFileSelect(e.target.files[0]);
                }
              }}
            />
          </label>
        )}
        
        {/* アセットから選択ボタン */}
        <button
          type="button"
          className={`px-3 py-2 ${showDirectUpload ? '' : 'rounded-r'} bg-green-500 text-white hover:bg-green-600`}
          onClick={() => setShowAssetPicker(!showAssetPicker)}
        >
          アセットから選択
        </button>
        
        {/* 音声プレビュー */}
        {isAudio && value && (
          <button
            type="button"
            className={`ml-2 px-3 py-2 rounded ${isPlaying ? 'bg-red-500' : 'bg-green-500'} text-white`}
            onClick={() => toggleAudioPreview(value)}
          >
            {isPlaying ? <Square size={16} /> : <Play size={16} />}
          </button>
        )}
      </div>
      
      {/* アセット選択ドロップダウン */}
      {showAssetPicker && (
        <div 
          ref={pickerRef}
          className="absolute z-50 mt-1 w-full max-h-64 overflow-y-auto bg-white border rounded shadow-lg"
        >
          <div className="p-2 border-b">
            <button
              className="w-full p-2 text-left bg-gray-100 hover:bg-gray-200 rounded"
              onClick={() => {
                clearValue();
                setShowAssetPicker(false);
              }}
            >
              <X size={14} className="inline mr-1" /> 選択をクリア
            </button>
          </div>
          
          {hasValidAssets() ? (
            getAssetCategories().map(category => {
              const [mainCategory, subCategory] = category.split('/');
              const files = assets?.[mainCategory]?.[subCategory];
              
              if (!files || Object.keys(files).length === 0) return null;
              
              return (
                <div key={category} className="p-2 border-b">
                  <h4 className="font-semibold text-gray-700 mb-1">{subCategory}</h4>
                  
                  {acceptTypes.includes('image') ? (
                    // 画像タイプの場合はサムネイル表示
                    <div className="grid grid-cols-3 gap-2">
                      {Object.entries(files).map(([fileName, fileData]) => (
                        <div 
                          key={fileName}
                          className="cursor-pointer border rounded p-1 hover:bg-gray-100"
                          onClick={() => {
                            onChange(fileData.url);
                            setShowAssetPicker(false);
                          }}
                        >
                          <div 
                            className="h-20 bg-contain bg-center bg-no-repeat"
                            style={{ backgroundImage: `url(${fileData.url})` }}
                          ></div>
                          <div className="text-xs truncate mt-1">{fileName}</div>
                        </div>
                      ))}
                    </div>
                  ) : (
                    // 音声タイプの場合はリスト表示
                    <div className="grid grid-cols-1 gap-1">
                      {Object.entries(files).map(([fileName, fileData]) => (
                        <div 
                          key={fileName}
                          className="flex items-center p-1 hover:bg-gray-100 rounded cursor-pointer"
                          onClick={() => {
                            onChange(fileData.url);
                            setShowAssetPicker(false);
                          }}
                        >
                          <div className="flex-1 text-sm truncate">{fileName}</div>
                          <button
                            type="button"
                            className="p-1 text-green-500 hover:text-green-700"
                            onClick={(e) => {
                              e.stopPropagation();
                              toggleAudioPreview(fileData.url);
                            }}
                          >
                            {isPlaying && audioRef.current?.src === fileData.url ? <Square size={14} /> : <Play size={14} />}
                          </button>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              );
            })
          ) : (
            <div className="p-4 text-center text-gray-500">
              アセットが登録されていません。<br />
              アセット管理タブからアセットを追加してください。
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default FileSelector;