// src/components/editors/DialogueEditor.js
import React from 'react';
import { Clock, Trash2, Volume2, Image as ImageIcon, Music } from 'lucide-react';
import FileSelector from '../common/FileSelector';

const DialogueEditor = ({ 
  dialogue, 
  index, 
  updateDialogue, 
  updateDialogueEffect,
  deleteDialogue,
  canDelete,
  characters,
  moveDialogueUp,
  moveDialogueDown,
  canMoveUp,
  canMoveDown,
  handleFileSelect,
  assets
}) => {
  return (
    <div className="bg-gray-50 border rounded p-3 mb-4">
      <div className="flex justify-between mb-2">
        <div className="font-bold text-sm text-gray-600">セリフ #{index + 1}</div>

        <div className="flex space-x-2">
          {/* 移動ボタン */}
          <button
            onClick={moveDialogueUp}
            disabled={!canMoveUp}
            className={`text-blue-500 hover:text-blue-700 ${!canMoveUp ? 'opacity-50 cursor-not-allowed' : ''}`}
            title="上に移動"
          >
            ↑
          </button>
          <button
            onClick={moveDialogueDown}
            disabled={!canMoveDown}
            className={`text-blue-500 hover:text-blue-700 ${!canMoveDown ? 'opacity-50 cursor-not-allowed' : ''}`}
            title="下に移動"
          >
            ↓
          </button>
          {/* 削除ボタン */}
          <button
            onClick={deleteDialogue}
            className="text-red-500 hover:text-red-700"
            disabled={!canDelete}
          >
            <Trash2 size={16} />
          </button>
        </div>
      </div>
      
      <div className="grid grid-cols-6 gap-4 mb-4">
        <div className="col-span-2">
          <label className="block text-sm font-medium text-gray-700 mb-1">キャラクター</label>
          <select
            value={dialogue.character}
            onChange={(e) => updateDialogue('character', e.target.value)}
            className="w-full p-2 border rounded"
          >
            <option value="">話者なし</option>
            {characters.map(char => (
              <option key={char.id} value={char.name}>{char.name}</option>
            ))}
          </select>
        </div>
        <div className="col-span-4">
          <label className="block text-sm font-medium text-gray-700 mb-1">セリフ</label>
          <textarea
            value={dialogue.text}
            onChange={(e) => updateDialogue('text', e.target.value)}
            className="w-full p-2 border rounded"
            rows={2}
          />
        </div>
      </div>
      
      {/* 効果音設定 */}
      <div className="mb-4">
        <label className="block text-sm font-medium text-gray-700 mb-1">効果音</label>
        <FileSelector
          value={dialogue.sound || ''}
          onChange={(value) => updateDialogue('sound', value)}
          placeholder="効果音ファイルのURLまたはパス"
          onFileSelect={(file) => handleFileSelect(file, (url) => updateDialogue('sound', url))}
          acceptTypes="audio/*"
          icon={<Volume2 size={16} />}
          assets={assets}
          showDirectUpload={false}
        />
      </div>

      {/* 背景変更設定 */}
      <div className="bg-gray-100 rounded p-3 mb-3">
        <div className="flex items-center mb-2">
          <input 
            type="checkbox"
            checked={dialogue.backgroundChange?.enabled}
            onChange={(e) => updateDialogueEffect('backgroundChange', 'enabled', e.target.checked)}
            className="mr-2"
          />
          <label className="text-sm font-medium text-gray-700 flex items-center">
            <ImageIcon size={14} className="mr-1" /> このセリフで背景を変更
          </label>
        </div>
        
        {dialogue.backgroundChange?.enabled && (
          <div className="pl-4 border-l-2 border-blue-200">
            <div className="mb-2">
              <label className="flex items-center text-xs mb-2">
                <input 
                  type="checkbox"
                  checked={dialogue.backgroundChange?.removeBackground || false}
                  onChange={(e) => {
                    updateDialogueEffect('backgroundChange', 'removeBackground', e.target.checked);
                    if (e.target.checked) {
                      updateDialogueEffect('backgroundChange', 'background', '');
                    }
                  }}
                  className="mr-1"
                />
                このセリフで背景を非表示にする
              </label>
              {!dialogue.backgroundChange?.removeBackground && (
                <>
                  <label className="block text-sm font-medium text-gray-700 mb-1">新しい背景</label>
                  <FileSelector
                    value={dialogue.backgroundChange.background || ''}
                    onChange={(value) => updateDialogueEffect('backgroundChange', 'background', value)}
                    placeholder="背景画像のURLまたはパス"
                    onFileSelect={(file) => handleFileSelect(file, (url) => updateDialogueEffect('backgroundChange', 'background', url))}
                    acceptTypes="image/*"
                    icon={<ImageIcon size={16} />}
                    assets={assets}
                    showDirectUpload={false}
                  />
                </>
              )}
            </div>
            
            <div className="grid grid-cols-2 gap-2">
              <div>
                <label className="block text-xs text-gray-600 mb-1">切替タイプ</label>
                <select 
                  value={dialogue.backgroundChange.fadeType}
                  onChange={(e) => updateDialogueEffect('backgroundChange', 'fadeType', e.target.value)}
                  className="w-full p-1 text-sm border rounded"
                >
                  <option value="none">切り替えのみ</option>
                  <option value="fade">フェード</option>
                  <option value="crossfade">クロスフェード</option>
                </select>
              </div>
              
              {dialogue.backgroundChange.fadeType !== 'none' && (
                <div>
                  <label className="block text-xs text-gray-600 mb-1">フェード時間 (ミリ秒)</label>
                  <input 
                    type="number"
                    value={dialogue.backgroundChange.fadeDuration}
                    onChange={(e) => updateDialogueEffect('backgroundChange', 'fadeDuration', parseInt(e.target.value))}
                    min="100"
                    max="5000"
                    step="100"
                    className="w-full p-1 text-sm border rounded"
                  />
                </div>
              )}
            </div>
          </div>
        )}
      </div>

      {/* BGM変更設定 */}
      <div className="bg-gray-100 rounded p-3 mb-3">
        <div className="flex items-center mb-2">
          <input 
            type="checkbox"
            checked={dialogue.bgmChange?.enabled}
            onChange={(e) => updateDialogueEffect('bgmChange', 'enabled', e.target.checked)}
            className="mr-2"
          />
          <label className="text-sm font-medium text-gray-700 flex items-center">
            <Music size={14} className="mr-1" /> このセリフでBGMを変更
          </label>
        </div>
        
        {dialogue.bgmChange?.enabled && (
          <div className="pl-4 border-l-2 border-blue-200">
            <div className="mb-2">
              <label className="flex items-center text-xs mb-2">
                <input 
                  type="checkbox"
                  checked={dialogue.bgmChange?.stopBgm || false}
                  onChange={(e) => {
                    updateDialogueEffect('bgmChange', 'stopBgm', e.target.checked);
                    if (e.target.checked) {
                      updateDialogueEffect('bgmChange', 'bgm', '');
                    }
                  }}
                  className="mr-1"
                />
                このセリフでBGMを停止する
              </label>
              {!dialogue.bgmChange?.stopBgm && (
                <>
                  <label className="block text-sm font-medium text-gray-700 mb-1">新しいBGM</label>
                  <FileSelector
                    value={dialogue.bgmChange.bgm || ''}
                    onChange={(value) => updateDialogueEffect('bgmChange', 'bgm', value)}
                    placeholder="BGMファイルのURLまたはパス"
                    onFileSelect={(file) => handleFileSelect(file, (url) => updateDialogueEffect('bgmChange', 'bgm', url))}
                    acceptTypes="audio/*"
                    icon={<Music size={16} />}
                    assets={assets}
                    showDirectUpload={false}
                  />
                </>
              )}
            </div>
            
            <div className="grid grid-cols-3 gap-2">
              <div>
                <label className="block text-xs text-gray-600 mb-1">切替タイプ</label>
                <select 
                  value={dialogue.bgmChange.fadeType}
                  onChange={(e) => updateDialogueEffect('bgmChange', 'fadeType', e.target.value)}
                  className="w-full p-1 text-sm border rounded"
                >
                  <option value="none">切り替えのみ</option>
                  <option value="crossfade">クロスフェード</option>
                </select>
              </div>
              
              {dialogue.bgmChange.fadeType !== 'none' && (
                <div>
                  <label className="block text-xs text-gray-600 mb-1">フェード時間 (ミリ秒)</label>
                  <input 
                    type="number"
                    value={dialogue.bgmChange.fadeDuration}
                    onChange={(e) => updateDialogueEffect('bgmChange', 'fadeDuration', parseInt(e.target.value))}
                    min="100"
                    max="5000"
                    step="100"
                    className="w-full p-1 text-sm border rounded"
                  />
                </div>
              )}
              
              <div>
                <label className="block text-xs text-gray-600 mb-1">音量 (0-1)</label>
                <input 
                  type="number"
                  value={dialogue.bgmChange.volume}
                  onChange={(e) => updateDialogueEffect('bgmChange', 'volume', parseFloat(e.target.value))}
                  min="0"
                  max="1"
                  step="0.1"
                  className="w-full p-1 text-sm border rounded"
                />
              </div>
            </div>
          </div>
        )}
      </div>
      
      {/* 特殊効果設定 */}
      <div className="bg-gray-100 rounded p-3">
        <div className="grid grid-cols-2 gap-4">
          {/* ウェイト設定 */}
          <div className="border rounded p-3 bg-white">
            <div className="flex items-center mb-2">
              <input 
                type="checkbox"
                checked={dialogue.wait?.enabled}
                onChange={(e) => updateDialogueEffect('wait', 'enabled', e.target.checked)}
                className="mr-2"
              />
              <label className="text-sm font-medium text-gray-700 flex items-center">
                <Clock size={14} className="mr-1" /> ウェイト設定
              </label>
            </div>
            
            {dialogue.wait?.enabled && (
              <>
                <div className="mb-2">
                  <label className="block text-xs text-gray-600 mb-1">ウェイト時間 (ミリ秒)</label>
                  <input 
                    type="number"
                    value={dialogue.wait?.duration || 2000}
                    onChange={(e) => updateDialogueEffect('wait', 'duration', parseInt(e.target.value))}
                    min="100"
                    max="10000"
                    step="100"
                    className="w-full p-1 text-sm border rounded"
                  />
                </div>
                <div>
                  <label className="flex items-center text-xs">
                    <input 
                      type="checkbox"
                      checked={dialogue.wait?.autoProgress}
                      onChange={(e) => updateDialogueEffect('wait', 'autoProgress', e.target.checked)}
                      className="mr-1"
                    />
                    ウェイト後に自動で次へ進む
                  </label>
                </div>
              </>
            )}
          </div>
          
          {/* フェード効果 */}
          <div className="border rounded p-3 bg-white">
            <div className="flex items-center mb-2">
              <input 
                type="checkbox"
                checked={dialogue.fade?.enabled}
                onChange={(e) => updateDialogueEffect('fade', 'enabled', e.target.checked)}
                className="mr-2"
              />
              <label className="text-sm font-medium text-gray-700">フェード効果</label>
            </div>
            
            {dialogue.fade?.enabled && (
              <div className="grid grid-cols-2 gap-2">
                <div>
                  <label className="block text-xs text-gray-600 mb-1">タイプ</label>
                  <select 
                    value={dialogue.fade?.type || 'in'}
                    onChange={(e) => updateDialogueEffect('fade', 'type', e.target.value)}
                    className="w-full p-1 text-sm border rounded"
                  >
                    <option value="in">フェードイン</option>
                    <option value="out">フェードアウト</option>
                  </select>
                </div>
                
                <div>
                  <label className="block text-xs text-gray-600 mb-1">色</label>
                  <select 
                    value={dialogue.fade?.color || 'black'}
                    onChange={(e) => updateDialogueEffect('fade', 'color', e.target.value)}
                    className="w-full p-1 text-sm border rounded"
                  >
                    <option value="black">黒</option>
                    <option value="white">白</option>
                  </select>
                </div>
                
                <div className="col-span-2">
                  <label className="block text-xs text-gray-600 mb-1">時間 (ミリ秒)</label>
                  <input 
                    type="number"
                    value={dialogue.fade?.duration || 1000}
                    onChange={(e) => updateDialogueEffect('fade', 'duration', parseInt(e.target.value))}
                    min="100"
                    max="5000"
                    step="100"
                    className="w-full p-1 text-sm border rounded"
                  />
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default DialogueEditor;