// src/components/preview/NovelPreview.js
import React, { useState, useEffect, useRef } from 'react';
import { ChevronRight, Clock } from 'lucide-react';
import { crossFadeBGM } from '../../utils/audioUtils';

const NovelPreview = ({ story, characters, initialScene = 0, startAt = 'opening', onClose }) => {
  const [gameState, setGameState] = useState(startAt); // 'opening', 'story', 'ending'
  const [currentScene, setCurrentScene] = useState(initialScene);
  const [dialogueIndex, setDialogueIndex] = useState(0);
  const [showChoices, setShowChoices] = useState(false);
  const [textProgress, setTextProgress] = useState('');
  const [isTypingComplete, setIsTypingComplete] = useState(false);
  
  // フェード関連のステート
  const [fading, setFading] = useState(false);
  const [fadeColor, setFadeColor] = useState('black');
  const [fadeOpacity, setFadeOpacity] = useState(0);
  
  // ウェイト関連のステート
  const [waiting, setWaiting] = useState(false);
  
  const bgmPlayerRef = useRef(null);
  const sePlayerRef = useRef(null);
  const typingIntervalRef = useRef(null);
  const waitTimerRef = useRef(null);
  const fadeTimerRef = useRef(null);
  
  const scene = story.scenes[currentScene];
  const dialogue = scene?.dialogues[dialogueIndex];
  
  // タイマーをクリア
  const clearAllTimers = () => {
    if (typingIntervalRef.current) {
      clearInterval(typingIntervalRef.current);
      typingIntervalRef.current = null;
    }
    if (waitTimerRef.current) {
      clearTimeout(waitTimerRef.current);
      waitTimerRef.current = null;
    }
    if (fadeTimerRef.current) {
      clearTimeout(fadeTimerRef.current);
      fadeTimerRef.current = null;
    }
  };
  
  // フェードイン効果
  const fadeIn = (color = 'black', duration = 1000) => {
    return new Promise((resolve) => {
      setFading(true);
      setFadeColor(color);
      setFadeOpacity(1);
      
      setTimeout(() => {
        setFadeOpacity(0);
        fadeTimerRef.current = setTimeout(() => {
          setFading(false);
          resolve();
        }, duration);
      }, 50);
    });
  };
  
  // フェードアウト効果
  const fadeOut = (color = 'black', duration = 1000) => {
    return new Promise((resolve) => {
      setFading(true);
      setFadeColor(color);
      setFadeOpacity(0);
      
      setTimeout(() => {
        setFadeOpacity(1);
        fadeTimerRef.current = setTimeout(() => {
          resolve();
        }, duration);
      }, 50);
    });
  };
  
  // ウェイト処理
  const wait = (duration = 2000) => {
    return new Promise((resolve) => {
      setWaiting(true);
      waitTimerRef.current = setTimeout(() => {
        setWaiting(false);
        resolve();
      }, duration);
    });
  };
  
  // 背景切り替え
  const changeBackground = async (bgSettings) => {
    if (!bgSettings || !bgSettings.enabled) return;
    
    const bgElement = document.getElementById('background');
    if (!bgElement) return;
    
    // 背景を非表示にする場合
    if (bgSettings.removeBackground) {
      // フェードが必要な場合はフェードアウト
      if (bgSettings.fadeType !== 'none') {
        await fadeOut('black', bgSettings.fadeDuration);
      }
      
      bgElement.style.backgroundImage = 'none';
      bgElement.style.backgroundColor = '#000000';
      
      // フェードインが必要な場合
      if (bgSettings.fadeType !== 'none') {
        await fadeIn('black', bgSettings.fadeDuration);
      }
      return;
    }
    
    // フェードタイプに応じた処理
    if (bgSettings.fadeType === 'fade') {
      // フェードアウト → 背景変更 → フェードイン
      await fadeOut('black', bgSettings.fadeDuration / 2);
      bgElement.style.backgroundImage = `url("${bgSettings.background}")`;
      await fadeIn('black', bgSettings.fadeDuration / 2);
    } else if (bgSettings.fadeType === 'crossfade') {
      // クロスフェード処理（簡易版）
      const tempBg = document.createElement('div');
      tempBg.className = 'absolute inset-0 bg-center bg-cover transition-opacity';
      tempBg.style.backgroundImage = bgElement.style.backgroundImage;
      tempBg.style.transition = `opacity ${bgSettings.fadeDuration}ms ease`;
      document.getElementById('novel-container').prepend(tempBg);
      
      bgElement.style.backgroundImage = `url("${bgSettings.background}")`;
      setTimeout(() => {
        tempBg.style.opacity = '0';
        setTimeout(() => {
          tempBg.remove();
        }, bgSettings.fadeDuration);
      }, 50);
      
      // クロスフェード完了を待機
      await wait(bgSettings.fadeDuration);
    } else {
      // 直接切り替え
      bgElement.style.backgroundImage = `url("${bgSettings.background}")`;
    }
  };
  
  // BGM切り替え
  const changeBGM = (bgmSettings) => {
    if (!bgmSettings || !bgmSettings.enabled) return;
    
    // BGM停止の場合
    if (bgmSettings.stopBgm) {
      if (bgmPlayerRef.current) {
        bgmPlayerRef.current.pause();
        bgmPlayerRef.current.src = '';
      }
      return;
    }
    
    if (bgmSettings.fadeType === 'crossfade' && bgmPlayerRef.current) {
      // クロスフェード処理
      const volume = bgmSettings.volume || 0.5;
      const duration = bgmSettings.fadeDuration || 1000;
      
      // 一時的なAudio要素を作成してクロスフェード
      const tempAudio = new Audio(bgmSettings.bgm);
      tempAudio.loop = true;
      tempAudio.volume = 0;
      
      // フェード処理を実行
      const oldVolume = bgmPlayerRef.current.volume;
      let time = 0;
      const step = 16; // 約60fps
      
      const fadeInterval = setInterval(() => {
        time += step;
        const progress = Math.min(time / duration, 1);
        
        // 古いBGMをフェードアウト
        bgmPlayerRef.current.volume = oldVolume * (1 - progress);
        
        // 新しいBGMをフェードイン
        tempAudio.volume = volume * progress;
        
        if (progress >= 1) {
          clearInterval(fadeInterval);
          
          // 古いBGMを停止
          bgmPlayerRef.current.pause();
          
          // 新しいBGMを設定
          bgmPlayerRef.current.src = bgmSettings.bgm;
          bgmPlayerRef.current.volume = volume;
          bgmPlayerRef.current.play().catch(e => console.log('BGM autoplay prevented'));
          
          // 一時的なAudioを停止
          tempAudio.pause();
          tempAudio.src = '';
        }
      }, step);
      
      // 一時的なAudioを再生
      tempAudio.play().catch(e => console.log('BGM autoplay prevented'));
    } else {
      // 直接切り替え
      if (bgmPlayerRef.current) {
        bgmPlayerRef.current.src = bgmSettings.bgm;
        bgmPlayerRef.current.volume = bgmSettings.volume || 0.5;
        bgmPlayerRef.current.play().catch(e => console.log('BGM autoplay prevented'));
      }
    }
  };
  
  // オープニング画面からゲーム開始
  const startGame = () => {
    setGameState('story');
    setCurrentScene(0);
    setDialogueIndex(0);
    if (bgmPlayerRef.current) {
      bgmPlayerRef.current.pause();
    }
  };
  
  // オープニングに戻る
  const returnToOpening = () => {
    setGameState('opening');
    if (bgmPlayerRef.current) {
      bgmPlayerRef.current.pause();
    }
  };
  
  // エンディングを表示
  const showEnding = () => {
    // フェード状態をリセット
    setFading(false);
    setFadeOpacity(0);
    
    // タイマーをクリア
    clearAllTimers();
    
    // エンディング表示処理
    setGameState('ending');
    
    if (bgmPlayerRef.current) {
      bgmPlayerRef.current.pause();
    }
    
    // エンディングBGM設定と再生
    if (story.ending.bgm && bgmPlayerRef.current) {
      bgmPlayerRef.current.src = story.ending.bgm;
      bgmPlayerRef.current.play().catch(e => console.log('BGM autoplay prevented'));
    }
  };
  
  // ゲーム状態に応じた処理
  useEffect(() => {
    // BGMを停止
    if (bgmPlayerRef.current) {
      bgmPlayerRef.current.pause();
    }
    
    if (gameState === 'opening') {
      // オープニングBGM設定
      if (story.opening.bgm && bgmPlayerRef.current) {
        bgmPlayerRef.current.src = story.opening.bgm;
        bgmPlayerRef.current.play().catch(e => console.log('BGM autoplay prevented'));
      }
    } else if (gameState === 'story') {
      // ストーリーの初期化
      const scene = story.scenes[currentScene];
      // BGM設定
      if (scene.bgm && bgmPlayerRef.current) {
        bgmPlayerRef.current.src = scene.bgm;
        bgmPlayerRef.current.play().catch(e => console.log('BGM autoplay prevented'));
      }
    } else if (gameState === 'ending') {
      // エンディングBGM設定
      if (story.ending.bgm && bgmPlayerRef.current) {
        bgmPlayerRef.current.src = story.ending.bgm;
        bgmPlayerRef.current.play().catch(e => console.log('BGM autoplay prevented'));
      }
    }
  }, [gameState, story]);
  
  // シーン変更時の処理
  useEffect(() => {
    if (gameState !== 'story' || !scene) return;
    
    const loadScene = async () => {
      // タイマーをクリア
      clearAllTimers();
      
      // BGM設定
      if (scene.bgm && bgmPlayerRef.current) {
        bgmPlayerRef.current.src = scene.bgm;
        bgmPlayerRef.current.volume = 0.5;
        try {
          await bgmPlayerRef.current.play();
        } catch (e) {
          console.log('BGM autoplay prevented by browser policy');
        }
      } else if (bgmPlayerRef.current) {
        bgmPlayerRef.current.pause();
      }
      
      // ダイアログ初期化
      setDialogueIndex(0);
      setShowChoices(false);
      setTextProgress('');
      setIsTypingComplete(false);
      setWaiting(false);
    };
    
    loadScene();
  }, [currentScene, scene, gameState]);
  
  // ダイアログが変わった時の処理
  useEffect(() => {
    if (gameState !== 'story' || !dialogue) return;
    
    // タイマーをクリア
    clearAllTimers();
    
    // タイプライター効果をリセット
    setTextProgress('');
    setIsTypingComplete(false);
    
    const processDialogue = async () => {
      // フェードイン効果
      if (dialogue.fade?.enabled && dialogue.fade.type === 'in') {
        await fadeIn(dialogue.fade.color, dialogue.fade.duration);
      }
      
      // 背景変更
      if (dialogue.backgroundChange?.enabled) {
        await changeBackground(dialogue.backgroundChange);
      }
      
      // BGM変更
      if (dialogue.bgmChange?.enabled) {
        changeBGM(dialogue.bgmChange);
      }
      
      // 効果音再生
      if (dialogue.sound && sePlayerRef.current) {
        sePlayerRef.current.src = dialogue.sound;
        sePlayerRef.current.volume = 0.7;
        sePlayerRef.current.play().catch(e => console.log('SE autoplay prevented'));
      }
      
      // タイプライター効果の開始
      startTypewriterEffect();
    };
    
    processDialogue();
    
    // コンポーネントのアンマウント時にタイマーをクリア
    return () => {
      clearAllTimers();
    };
  }, [dialogueIndex, dialogue, gameState]);
  
  // タイプライター効果の開始
  const startTypewriterEffect = () => {
    if (!dialogue) return;
    
    // 前のタイプライターをクリア
    clearInterval(typingIntervalRef.current);
    
    let charIndex = 0;
    
    typingIntervalRef.current = setInterval(() => {
      if (charIndex < dialogue.text.length) {
        setTextProgress(dialogue.text.substring(0, charIndex + 1));
        charIndex++;
      } else {
        // タイピング完了
        clearInterval(typingIntervalRef.current);
        setIsTypingComplete(true);
        
        // ウェイト効果
        if (dialogue.wait?.enabled) {
          setWaiting(true);
          waitTimerRef.current = setTimeout(() => {
            setWaiting(false);
            
            // 自動進行
            if (dialogue.wait.autoProgress) {
              nextDialogue();
            }
          }, dialogue.wait.duration);
        }
        
        // フェードアウト効果
        if (dialogue.fade?.enabled && dialogue.fade.type === 'out') {
          const delay = dialogue.wait?.enabled ? dialogue.wait.duration : 1000;
          fadeTimerRef.current = setTimeout(() => {
            fadeOut(dialogue.fade.color, dialogue.fade.duration).then(() => {
              nextDialogue();
            });
          }, delay);
        }
      }
    }, 50); // タイピング速度
  };
  
  // 次のダイアログを表示
  const nextDialogue = () => {
    if (gameState !== 'story' || !scene) return;
    
    // フェード中またはウェイト中は無視
    if (fading || waiting) return;
    
    // タイマーをクリア
    clearAllTimers();
    
    // タイピング途中ならスキップ
    if (!isTypingComplete && dialogue) {
      setTextProgress(dialogue.text);
      setIsTypingComplete(true);
      return;
    }
    
    // 最後のダイアログなら選択肢を表示
    if (dialogueIndex >= scene.dialogues.length - 1) {
      setDialogueIndex(dialogueIndex + 1);
      setShowChoices(true);
      
      if (!scene.choices || scene.choices.length === 0) {
        // 選択肢がない場合
        
        // 次のシーンが指定されている場合は自動的に遷移
        if (scene.nextScene) {
          const nextSceneIndex = story.scenes.findIndex(s => s.id === scene.nextScene);
          if (nextSceneIndex !== -1) {
            setCurrentScene(nextSceneIndex);
            setDialogueIndex(0);
            return;
          }
        }
        
        // 選択肢がなく、次のシーンが指定されていなければエンディングへ
        if (story.ending.enabled) {
          // フェード中なら完了を待つ
          if (fading) {
            // フェードが完了してからエンディングへ
            fadeTimerRef.current = setTimeout(() => {
              setFading(false);
              setFadeOpacity(0);
              showEnding();
            }, 1000);
          } else {
            showEnding();
          }
          return;
        }
      }
      
      return;
    }
    
    // 次のダイアログへ
    setDialogueIndex(dialogueIndex + 1);
  };
  
  // 選択肢を選んだ時の処理
  const selectChoice = (choice) => {
    const nextSceneId = choice.nextScene;
    if (!nextSceneId) return;
    
    const nextSceneIndex = story.scenes.findIndex(s => s.id === nextSceneId);
    if (nextSceneIndex === -1) return;
    
    setCurrentScene(nextSceneIndex);
    setDialogueIndex(0);
  };
  
  // 話者のキャラクター情報を取得
  const getCharacter = (name) => {
    return characters.find(c => c.name === name) || null;
  };
  
  // 現在の話者
  const speaker = dialogue?.character ? getCharacter(dialogue.character) : null;
  
  // クリック処理
  const handleClick = () => {
    if (gameState === 'story') {
      nextDialogue();
    }
  };
  
  // コンポーネントのアンマウント時にタイマーをクリア
  useEffect(() => {
    return () => {
      clearAllTimers();
      if (bgmPlayerRef.current) {
        bgmPlayerRef.current.pause();
      }
    };
  }, []);
  
  // プレビュー画面
  return (
    <div className="flex-1 relative overflow-hidden" id="novel-container">
      {/* ゲーム状態によって表示を切り替え */}
      {gameState === 'opening' && (
        <div 
          className="absolute inset-0 flex flex-col items-center justify-center bg-black"
          style={{
            backgroundImage: story.opening.background ? `url(${story.opening.background})` : 'none',
            backgroundSize: 'cover',
            backgroundPosition: 'center'
          }}
        >
          {story.opening.logoImage && (
            <img 
              src={story.opening.logoImage} 
              alt="ロゴ" 
              className="max-w-md mb-6" 
            />
          )}
          
          <h1 
            className="text-4xl font-bold mb-4"
            style={{ color: story.opening.titleColor }}
          >
            {story.opening.title || story.title}
          </h1>
          
          {story.opening.subtitle && (
            <p 
              className="text-xl mb-8"
              style={{ color: story.opening.subtitleColor }}
            >
              {story.opening.subtitle}
            </p>
          )}
          
          <button
            className="px-6 py-3 rounded text-lg"
            style={{
              color: story.opening.startButtonColor,
              backgroundColor: story.opening.startButtonBgColor
            }}
            onClick={startGame}
          >
            {story.opening.startButtonText}
          </button>
        </div>
      )}
      
      {gameState === 'ending' && (
        <div 
          className="absolute inset-0 flex flex-col items-center justify-center bg-black"
          style={{
            backgroundImage: story.ending.background ? `url(${story.ending.background})` : 'none',
            backgroundSize: 'cover',
            backgroundPosition: 'center'
          }}
        >
          <h1 
            className="text-4xl font-bold mb-4"
            style={{ color: story.ending.messageColor }}
          >
            {story.ending.message}
          </h1>
          
          {story.ending.credit && (
            <p 
              className="text-xl mb-8"
              style={{ color: story.ending.creditColor }}
            >
              {story.ending.credit}
            </p>
          )}
          
          <button
            className="px-6 py-3 rounded text-lg"
            style={{
              color: story.ending.returnButtonColor,
              backgroundColor: story.ending.returnButtonBgColor
            }}
            onClick={returnToOpening}
          >
            {story.ending.returnButtonText}
          </button>
        </div>
      )}
      
      {gameState === 'story' && scene && (
        <>
          {/* 背景 */}
          <div 
            id="background"
            className="absolute inset-0 bg-black bg-center bg-cover"
            style={{ backgroundImage: scene.background ? `url(${scene.background})` : 'none' }}
            onClick={handleClick}
          ></div>
          
          {/* キャラクター */}
          {speaker && speaker.image && (
            <div id="character" className="absolute bottom-0 left-1/2 transform -translate-x-1/2 h-3/4">
              <img 
                src={speaker.image} 
                alt={speaker.name} 
                className="h-full object-contain"
              />
            </div>
          )}
          
          {/* テキストボックス */}
          <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 w-11/12 p-4 bg-black bg-opacity-70 text-white rounded-lg">
            {dialogue?.character && (
              <div id="speaker" className="text-yellow-300 font-bold mb-2">{dialogue.character}</div>
            )}
            <div id="text" className="min-h-16">
              {textProgress}
            </div>
            
            {/* 続きを示すアイコン（タイピング終了時・ウェイト中でない時のみ） */}
            {isTypingComplete && !waiting && !showChoices && !fading && (
              <div className="absolute bottom-2 right-4 animate-bounce">
                <ChevronRight size={24} />
              </div>
            )}
            
            {/* ウェイトインジケータ */}
            {waiting && (
              <div className="absolute bottom-2 right-4">
                <Clock size={24} className="text-gray-400" />
              </div>
            )}
          </div>
          
          {/* 選択肢 */}
          {showChoices && scene.choices && scene.choices.length > 0 && (
            <div id="choices" className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-3/4">
              {scene.choices.map((choice, index) => (
                <button
                  key={index}
                  onClick={() => selectChoice(choice)}
                  className="block w-full mb-3 p-3 bg-gray-800 bg-opacity-90 text-white text-left rounded hover:bg-gray-700 transition"
                >
                  {choice.text}
                </button>
              ))}
            </div>
          )}
          
          {/* フェードオーバーレイ */}
          <div 
            className="absolute inset-0 z-50 pointer-events-none transition-opacity duration-1000"
            style={{
              backgroundColor: fadeColor,
              opacity: fadeOpacity,
              pointerEvents: fading ? 'auto' : 'none'
            }}
          ></div>
        </>
      )}
      
      {/* 戻るボタン */}
      <button
        onClick={onClose}
        className="absolute top-4 right-4 px-3 py-1 bg-gray-800 bg-opacity-70 text-white rounded hover:bg-gray-700"
      >
        エディタに戻る
      </button>
      
      {/* オーディオプレーヤー */}
      <audio ref={bgmPlayerRef} loop />
      <audio ref={sePlayerRef} />
    </div>
  );
};

export default NovelPreview;