// src/components/editors/CharacterEditor.js
import React from 'react';
import { Trash2, Image as ImageIcon } from 'lucide-react';
import FileSelector from '../common/FileSelector';

const CharacterEditor = ({
  characters,
  updateCharacter,
  addCharacter,
  deleteCharacter,
  handleFileSelect,
  assets
}) => {
  return (
    <div>
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-lg font-bold">キャラクター設定</h2>
        <button
          onClick={addCharacter}
          className="px-2 py-1 bg-green-500 text-white rounded hover:bg-green-600 flex items-center"
        >
          <span className="mr-1">+</span> キャラクター追加
        </button>
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {characters.map((character, index) => (
          <div key={character.id} className="bg-white rounded-lg shadow p-4">
            <div className="flex justify-between mb-4">
              <h3 className="font-bold">キャラクター #{index + 1}</h3>
              <button
                onClick={() => deleteCharacter(index)}
                className="text-red-500 hover:text-red-700"
                disabled={characters.length <= 1}
              >
                <Trash2 size={16} />
              </button>
            </div>
            
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">名前</label>
              <input
                type="text"
                value={character.name}
                onChange={(e) => updateCharacter(index, 'name', e.target.value)}
                className="w-full p-2 border rounded"
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">キャラクター画像</label>
              <FileSelector
                value={character.image}
                onChange={(value) => updateCharacter(index, 'image', value)}
                placeholder="画像のURLまたはパス"
                onFileSelect={(file) => handleFileSelect(file, (url) => updateCharacter(index, 'image', url))}
                acceptTypes="image/*"
                icon={<ImageIcon size={16} />}
                assets={assets}
                showDirectUpload={false}
              />
              
              {/* キャラクター画像プレビュー */}
              <div 
                className="w-full h-40 mt-2 bg-center bg-contain bg-no-repeat border rounded flex items-center justify-center text-gray-400"
                style={{
                  backgroundImage: character.image ? `url(${character.image})` : 'none',
                  backgroundColor: character.image ? 'transparent' : '#f9fafb'
                }}
              >
                {!character.image && <ImageIcon size={48} />}
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default CharacterEditor;