// src/components/editors/OpeningEditor.js
import React from 'react';
import FileSelector from '../common/FileSelector';
import { Image, Music } from 'lucide-react';

const OpeningEditor = ({ opening, updateOpening, handleFileSelect, assets }) => {
  return (
    <div>
      <h2 className="text-lg font-bold mb-4">オープニング画面設定</h2>
      
      <div className="bg-white rounded-lg shadow p-4 mb-6">
        <div className="mb-4">
          <label className="flex items-center">
            <input 
              type="checkbox"
              checked={opening.enabled}
              onChange={(e) => updateOpening('enabled', e.target.checked)}
              className="mr-2"
            />
            オープニング画面を表示する
          </label>
        </div>
        
        {opening.enabled && (
          <>
            {/* 背景とBGM */}
            <div className="grid grid-cols-2 gap-4 mb-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">背景画像</label>
                <FileSelector
                  value={opening.background}
                  onChange={(value) => updateOpening('background', value)}
                  placeholder="画像のURLまたはパス"
                  onFileSelect={(file) => handleFileSelect(file, (url) => updateOpening('background', url))}
                  acceptTypes="image/*"
                  icon={<Image size={16} />}
                  assets={assets}
                  showDirectUpload={false} // 直接アップロードは非表示
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">BGM</label>
                <FileSelector
                  value={opening.bgm}
                  onChange={(value) => updateOpening('bgm', value)}
                  placeholder="音声ファイルのURLまたはパス"
                  onFileSelect={(file) => handleFileSelect(file, (url) => updateOpening('bgm', url))}
                  acceptTypes="audio/*"
                  icon={<Music size={16} />}
                  assets={assets}
                  showDirectUpload={false} // 直接アップロードは非表示
                />
              </div>
            </div>
            
            {/* ロゴ画像 */}
            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-1">ロゴ画像（任意）</label>

              <FileSelector
                value={opening.logoImage}
                onChange={(value) => updateOpening('logoImage', value)}
                placeholder="画像のURLまたはパス"
                onFileSelect={(file) => handleFileSelect(file, (url) => updateOpening('logoImage', url))}
                acceptTypes="image/*"
                icon={<Image size={16} />}
                assets={assets}
                showDirectUpload={false} // 直接アップロードは非表示
              />
              {/* ロゴプレビュー */}
              {opening.logoImage && (
                <div className="border rounded p-2 bg-gray-50 mb-4 mt-2">
                  <img 
                    src={opening.logoImage} 
                    alt="ロゴ" 
                    className="max-h-40 mx-auto"
                  />
                </div>
              )}
            </div>
            
            {/* タイトルとサブタイトル */}
            <div className="grid grid-cols-2 gap-4 mb-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">タイトル</label>
                <input
                  type="text"
                  value={opening.title}
                  onChange={(e) => updateOpening('title', e.target.value)}
                  placeholder="タイトルテキスト"
                  className="w-full p-2 border rounded"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">タイトル色</label>
                <input
                  type="color"
                  value={opening.titleColor}
                  onChange={(e) => updateOpening('titleColor', e.target.value)}
                  className="w-full p-1 border rounded h-10"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">サブタイトル</label>
                <input
                  type="text"
                  value={opening.subtitle}
                  onChange={(e) => updateOpening('subtitle', e.target.value)}
                  placeholder="サブタイトルテキスト"
                  className="w-full p-2 border rounded"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">サブタイトル色</label>
                <input
                  type="color"
                  value={opening.subtitleColor}
                  onChange={(e) => updateOpening('subtitleColor', e.target.value)}
                  className="w-full p-1 border rounded h-10"
                />
              </div>
            </div>
            
            {/* スタートボタン設定 */}
            <div className="grid grid-cols-3 gap-4 mb-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">スタートボタンテキスト</label>
                <input
                  type="text"
                  value={opening.startButtonText}
                  onChange={(e) => updateOpening('startButtonText', e.target.value)}
                  placeholder="ボタンテキスト"
                  className="w-full p-2 border rounded"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">ボタン文字色</label>
                <input
                  type="color"
                  value={opening.startButtonColor}
                  onChange={(e) => updateOpening('startButtonColor', e.target.value)}
                  className="w-full p-1 border rounded h-10"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">ボタン背景色</label>
                <input
                  type="color"
                  value={opening.startButtonBgColor}
                  onChange={(e) => updateOpening('startButtonBgColor', e.target.value)}
                  className="w-full p-1 border rounded h-10"
                />
              </div>
            </div>
            
            {/* アニメーション設定 */}
            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-1">フェードイン時間 (ミリ秒)</label>
              <input
                type="number"
                value={opening.fadeInDuration}
                onChange={(e) => updateOpening('fadeInDuration', parseInt(e.target.value))}
                min="500"
                max="5000"
                step="100"
                className="w-full p-2 border rounded"
              />
            </div>
            
            {/* プレビュー */}
            <div className="border rounded p-4 bg-gray-50">
              <h3 className="font-bold mb-4">プレビュー</h3>
              <div 
                className="relative w-full h-64 rounded overflow-hidden flex flex-col items-center justify-center"
                style={{
                  backgroundColor: '#000',
                  backgroundImage: opening.background ? `url(${opening.background})` : 'none',
                  backgroundSize: 'cover',
                  backgroundPosition: 'center'
                }}
              >
                {opening.logoImage && (
                  <img 
                    src={opening.logoImage}
                    alt="ロゴ"
                    className="max-h-20 mb-4"
                  />
                )}
                
                <div 
                  className="text-2xl font-bold mb-2"
                  style={{ color: opening.titleColor }}
                >
                  {opening.title || '(タイトルなし)'}
                </div>
                
                {opening.subtitle && (
                  <div 
                    className="text-lg mb-4"
                    style={{ color: opening.subtitleColor }}
                  >
                    {opening.subtitle}
                  </div>
                )}
                
                <button
                  className="px-4 py-2 rounded"
                  style={{
                    color: opening.startButtonColor,
                    backgroundColor: opening.startButtonBgColor
                  }}
                >
                  {opening.startButtonText}
                </button>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default OpeningEditor;