// src/components/editors/EndingEditor.js
import React from 'react';
import FileSelector from '../common/FileSelector';
import { Image, Music } from 'lucide-react';

const EndingEditor = ({ ending, updateEnding, handleFileSelect, assets }) => {
  return (
    <div>
      <h2 className="text-lg font-bold mb-4">エンディング画面設定</h2>
      
      <div className="bg-white rounded-lg shadow p-4 mb-6">
        <div className="mb-4">
          <label className="flex items-center">
            <input 
              type="checkbox"
              checked={ending.enabled}
              onChange={(e) => updateEnding('enabled', e.target.checked)}
              className="mr-2"
            />
            エンディング画面を表示する
          </label>
        </div>
        
        {ending.enabled && (
          <>
            {/* 背景とBGM */}
            <div className="grid grid-cols-2 gap-4 mb-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">背景画像</label>

                <FileSelector
                  value={ending.background}
                  onChange={(value) => updateEnding('background', value)}
                  placeholder="画像のURLまたはパス"
                  onFileSelect={(file) => handleFileSelect(file, (url) => updateEnding('background', url))}
                  acceptTypes="image/*"
                  icon={<Image size={16} />}
                  assets={assets}
                  showDirectUpload={false} // 直接アップロードは非表示
                />

              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">BGM</label>


                <FileSelector
                  value={ending.bgm}
                  onChange={(value) => updateEnding('bgm', value)}
                  placeholder="音声ファイルのURLまたはパス"
                  onFileSelect={(file) => handleFileSelect(file, (url) => updateEnding('bgm', url))}
                  acceptTypes="audio/*"
                  icon={<Music size={16} />}
                  assets={assets}
                  showDirectUpload={false} // 直接アップロードは非表示
                />

              </div>
            </div>
            
            {/* メッセージとクレジット */}
            <div className="grid grid-cols-2 gap-4 mb-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">メッセージ</label>
                <input
                  type="text"
                  value={ending.message}
                  onChange={(e) => updateEnding('message', e.target.value)}
                  placeholder="エンディングメッセージ"
                  className="w-full p-2 border rounded"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">メッセージ色</label>
                <input
                  type="color"
                  value={ending.messageColor}
                  onChange={(e) => updateEnding('messageColor', e.target.value)}
                  className="w-full p-1 border rounded h-10"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">クレジット</label>
                <input
                  type="text"
                  value={ending.credit}
                  onChange={(e) => updateEnding('credit', e.target.value)}
                  placeholder="クレジットテキスト"
                  className="w-full p-2 border rounded"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">クレジット色</label>
                <input
                  type="color"
                  value={ending.creditColor}
                  onChange={(e) => updateEnding('creditColor', e.target.value)}
                  className="w-full p-1 border rounded h-10"
                />
              </div>
            </div>
            
            {/* 戻るボタン設定 */}
            <div className="grid grid-cols-3 gap-4 mb-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">戻るボタンテキスト</label>
                <input
                  type="text"
                  value={ending.returnButtonText}
                  onChange={(e) => updateEnding('returnButtonText', e.target.value)}
                  placeholder="ボタンテキスト"
                  className="w-full p-2 border rounded"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">ボタン文字色</label>
                <input
                  type="color"
                  value={ending.returnButtonColor}
                  onChange={(e) => updateEnding('returnButtonColor', e.target.value)}
                  className="w-full p-1 border rounded h-10"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">ボタン背景色</label>
                <input
                  type="color"
                  value={ending.returnButtonBgColor}
                  onChange={(e) => updateEnding('returnButtonBgColor', e.target.value)}
                  className="w-full p-1 border rounded h-10"
                />
              </div>
            </div>
            
            {/* アニメーション設定 */}
            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-1">フェードイン時間 (ミリ秒)</label>
              <input
                type="number"
                value={ending.fadeInDuration}
                onChange={(e) => updateEnding('fadeInDuration', parseInt(e.target.value))}
                min="500"
                max="5000"
                step="100"
                className="w-full p-2 border rounded"
              />
            </div>
            
            {/* プレビュー */}
            <div className="border rounded p-4 bg-gray-50">
              <h3 className="font-bold mb-4">プレビュー</h3>
              <div 
                className="relative w-full h-64 rounded overflow-hidden flex flex-col items-center justify-center"
                style={{
                  backgroundColor: '#000',
                  backgroundImage: ending.background ? `url(${ending.background})` : 'none',
                  backgroundSize: 'cover',
                  backgroundPosition: 'center'
                }}
              >
                <div 
                  className="text-2xl font-bold mb-2"
                  style={{ color: ending.messageColor }}
                >
                  {ending.message || '(メッセージなし)'}
                </div>
                
                {ending.credit && (
                  <div 
                    className="text-lg mb-4"
                    style={{ color: ending.creditColor }}
                  >
                    {ending.credit}
                  </div>
                )}
                
                <button
                  className="px-4 py-2 rounded"
                  style={{
                    color: ending.returnButtonColor,
                    backgroundColor: ending.returnButtonBgColor
                  }}
                >
                  {ending.returnButtonText}
                </button>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default EndingEditor;