// src/components/editors/StoryEditor.js
import React from 'react';
import { Plus, Trash2, ChevronLeft, ChevronRight, Image, Music } from 'lucide-react';
import DialogueEditor from './DialogueEditor';
import FileSelector from '../common/FileSelector';

const StoryEditor = ({ 
  story,
  currentScene,
  characters,
  updateTitle,
  updateSceneBackground,
  updateSceneBGM,
  updateSceneNextScene,
  updateDialogue,
  updateDialogueEffect,
  updateChoice,
  addDialogue,
  addChoice,
  deleteDialogue,
  deleteChoice,
  addScene,
  deleteScene,
  prevScene,
  nextScene,
  handleFileSelect,
  moveDialogueUp,
  moveDialogueDown,
  assets
}) => {
  const activeScene = story.scenes[currentScene];
  
  return (
    <div>
      {/* ストーリータイトル */}
      <div className="mb-6">
        <label className="block text-sm font-medium text-gray-700 mb-1">ストーリータイトル</label>
        <input
          type="text"
          value={story.title}
          onChange={(e) => updateTitle(e.target.value)}
          className="w-full p-2 border rounded"
        />
      </div>
      
      {/* シーン管理 */}
      <div className="bg-white rounded-lg shadow p-4 mb-6">
        <div className="flex justify-between items-center mb-4">
          <div className="flex items-center">
            <h2 className="text-lg font-bold">シーン {currentScene + 1}/{story.scenes.length}</h2>
            <div className="ml-4 flex space-x-2">
              <button 
                onClick={prevScene}
                disabled={currentScene === 0}
                className={`p-1 rounded ${currentScene === 0 ? 'bg-gray-200 text-gray-400' : 'bg-gray-200 hover:bg-gray-300'}`}
              >
                <ChevronLeft size={20} />
              </button>
              <button 
                onClick={nextScene}
                disabled={currentScene === story.scenes.length - 1}
                className={`p-1 rounded ${currentScene === story.scenes.length - 1 ? 'bg-gray-200 text-gray-400' : 'bg-gray-200 hover:bg-gray-300'}`}
              >
                <ChevronRight size={20} />
              </button>
            </div>
          </div>
          <div className="flex space-x-2">
            <button
              onClick={addScene}
              className="px-2 py-1 bg-green-500 text-white rounded hover:bg-green-600 flex items-center"
            >
              <Plus size={16} className="mr-1" /> シーン追加
            </button>
            <button
              onClick={() => deleteScene(currentScene)}
              className="px-2 py-1 bg-red-500 text-white rounded hover:bg-red-600 flex items-center"
              disabled={story.scenes.length <= 1}
            >
              <Trash2 size={16} className="mr-1" /> 削除
            </button>
          </div>
        </div>
        
        {/* 背景とBGM */}
        <div className="grid grid-cols-2 gap-4 mb-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">背景画像</label>
            <FileSelector
              value={activeScene.background}
              onChange={updateSceneBackground}
              placeholder="画像のURLまたはパス"
              onFileSelect={(file) => handleFileSelect(file, updateSceneBackground)}
              acceptTypes="image/*"
              icon={<Image size={16} />}
              assets={assets}
              showDirectUpload={false}
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">BGM</label>
            <FileSelector
              value={activeScene.bgm}
              onChange={updateSceneBGM}
              placeholder="音声ファイルのURLまたはパス"
              onFileSelect={(file) => handleFileSelect(file, updateSceneBGM)}
              acceptTypes="audio/*"
              icon={<Music size={16} />}
              assets={assets}
              showDirectUpload={false}
            />
          </div>
        </div>
        
        {/* シーンプレビュー */}
        <div className="mb-6 border rounded p-2 bg-gray-50">
          <div 
            className="w-full h-40 bg-center bg-cover flex items-center justify-center text-gray-400"
            style={{
              backgroundImage: activeScene.background ? `url(${activeScene.background})` : 'none',
              backgroundColor: activeScene.background ? 'transparent' : '#e5e7eb'
            }}
          >
            {!activeScene.background && <Image size={48} />}
          </div>
        </div>
        
        {/* 選択肢なしの場合の次のシーン設定 */}
        <div className="mb-6 bg-blue-50 border border-blue-200 rounded p-3">
          <div className="flex items-center mb-2">
            <label className="text-sm font-medium text-gray-700">
              選択肢なしでの次のシーン（選択肢がない場合に自動的に進むシーン）
            </label>
          </div>
          <select
            value={activeScene.nextScene || ''}
            onChange={(e) => updateSceneNextScene(e.target.value)}
            className="w-full p-2 border rounded"
          >
            <option value="">選択しない（最後のシーンはエンディングへ）</option>
            {story.scenes.filter(scene => scene.id !== activeScene.id).map((scene, sceneIndex) => {
              const actualIndex = story.scenes.findIndex(s => s.id === scene.id);
              return (
                <option key={scene.id} value={scene.id}>
                  シーン {actualIndex + 1}
                </option>
              );
            })}
          </select>
          <p className="text-sm text-gray-500 mt-1">
            ※選択肢がない場合、ここで指定したシーンに自動的に進みます。
            最後のシーンで選択肢がない場合はエンディングに進みます。
          </p>
        </div>
        
        // セリフリストと追加ボタンの修正部分
        {/* ダイアログリスト */}
        <div className="mb-6">
          <div className="flex justify-between items-center mb-4">
            <h3 className="font-bold text-lg">セリフ</h3>
            <button
              onClick={addDialogue}
              className="px-3 py-2 bg-green-500 text-white rounded hover:bg-green-600 flex items-center"
            >
              <Plus size={16} className="mr-1" /> セリフ追加
            </button>
          </div>
          
          {activeScene.dialogues.map((dialogue, index) => (
            <DialogueEditor
              key={index}
              dialogue={dialogue}
              index={index}
              updateDialogue={(field, value) => updateDialogue(index, field, value)}
              updateDialogueEffect={(effectType, field, value) => updateDialogueEffect(index, effectType, field, value)}
              deleteDialogue={() => deleteDialogue(index)}
              canDelete={activeScene.dialogues.length > 1}
              characters={characters}
              moveDialogueUp={() => moveDialogueUp(currentScene, index)}
              moveDialogueDown={() => moveDialogueDown(currentScene, index)}
              canMoveUp={index > 0}
              canMoveDown={index < activeScene.dialogues.length - 1}
              handleFileSelect={handleFileSelect}
              assets={assets}
            />
          ))}
          
          {/* セリフ追加ボタン（下部にも追加） */}
          <div className="mt-4 flex justify-center">
            <button
              onClick={addDialogue}
              className="px-4 py-2 bg-green-500 text-white rounded-full hover:bg-green-600 flex items-center shadow-md"
            >
              <Plus size={20} className="mr-2" /> セリフを追加する
            </button>
          </div>
        </div>
        
        {/* 選択肢リスト */}
        <div>
          <div className="flex justify-between items-center mb-2">
            <h3 className="font-bold">選択肢</h3>
            <button
              onClick={addChoice}
              className="px-2 py-1 bg-green-500 text-white rounded hover:bg-green-600 flex items-center text-sm"
            >
              <Plus size={14} className="mr-1" /> 選択肢追加
            </button>
          </div>
          
          {activeScene.choices && activeScene.choices.length > 0 ? (
            activeScene.choices.map((choice, index) => (
              <div key={index} className="bg-yellow-50 border border-yellow-200 rounded p-3 mb-3">
                <div className="flex justify-between mb-2">
                  <div className="font-bold text-sm text-gray-600">選択肢 #{index + 1}</div>
                  <button
                    onClick={() => deleteChoice(index)}
                    className="text-red-500 hover:text-red-700"
                  >
                    <Trash2 size={16} />
                  </button>
                </div>
                
                <div className="grid grid-cols-3 gap-4">
                  <div className="col-span-2">
                    <label className="block text-sm font-medium text-gray-700 mb-1">テキスト</label>
                    <input
                      type="text"
                      value={choice.text}
                      onChange={(e) => updateChoice(index, 'text', e.target.value)}
                      className="w-full p-2 border rounded"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">次のシーン</label>
                    <select
                      value={choice.nextScene}
                      onChange={(e) => updateChoice(index, 'nextScene', e.target.value)}
                      className="w-full p-2 border rounded"
                    >
                      <option value="">選択してください</option>
                      {story.scenes.map((scene, sceneIndex) => (
                        <option key={scene.id} value={scene.id}>
                          シーン {sceneIndex + 1}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
              </div>
            ))
          ) : (
            <div className="text-gray-500 text-center py-4 border border-dashed rounded">
              選択肢がありません（「選択肢なしでの次のシーン」設定で自動的に進みます）
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default StoryEditor;