// src/components/NovelMaker.js
import React, { useState, useEffect, useRef } from 'react';
import { 
  Pen, Book, Image, Music, FileText, Play, Save, 
  Upload, Plus, Trash2, ChevronRight, ChevronLeft, 
  Users, Volume2, Download, Clock, Settings, Home, Flag 
} from 'lucide-react';
import OpeningEditor from './editors/OpeningEditor';
import EndingEditor from './editors/EndingEditor';
import StoryEditor from './editors/StoryEditor';
import CharacterEditor from './editors/CharacterEditor';
import AssetManager from './AssetManager';
import NovelPreview from './preview/NovelPreview';
import { initialStoryState, initialCharacters } from '../models/storyModel';
import { processPathsForExport, addAssetsToZip, getAssetPath } from '../utils/exportUtils';

// JSZipとFileSaverをインポート（npm install jszip file-saver が必要）
import JSZip from 'jszip';
import { saveAs } from 'file-saver';

// メインアプリケーション
const NovelMaker = () => {
  const [activeTab, setActiveTab] = useState('story');
  const [story, setStory] = useState(initialStoryState);
  const [currentScene, setCurrentScene] = useState(0);
  const [characters, setCharacters] = useState(initialCharacters);
  const [previewMode, setPreviewMode] = useState(false);
  const [previewStartAt, setPreviewStartAt] = useState('opening'); // 'opening', 'story', 'ending'
  const [exportOptions, setExportOptions] = useState({
    showDialog: false,
    useRelativePaths: true,
    assetsFolder: 'assets'
  });
  
  // アセット管理のための状態
  const [assets, setAssets] = useState(() => {
    // ローカルストレージからアセット情報を復元
    const savedAssets = localStorage.getItem('novel_maker_assets');
    return savedAssets ? JSON.parse(savedAssets) : {
      image: { bg: {}, character: {} },
      sound: { bgm: {}, se: {} }
    };
  });
  
  // アクティブなシーン
  const activeScene = story.scenes[currentScene];

  // ファイル選択の共通処理を行う関数
  const handleFileSelect = (file, updateFunction) => {
    if (file) {
      const blobUrl = URL.createObjectURL(file);
      
      // アセットに追加
      const fileType = file.type.startsWith('image/') ? 'image' : 'sound';
      const subType = fileType === 'image' ? 'bg' : 'bgm'; // デフォルトサブタイプ
      
      const newAssets = { ...assets };
      if (!newAssets[fileType]) newAssets[fileType] = {};
      if (!newAssets[fileType][subType]) newAssets[fileType][subType] = {};
      
      newAssets[fileType][subType][file.name] = {
        url: blobUrl,
        type: file.type,
        size: file.size,
        lastModified: file.lastModified
      };
      
      setAssets(newAssets);
      
      // 更新関数を呼び出してURLを設定
      updateFunction(blobUrl);
    }
  };
  
  // シーンを追加
  const addScene = () => {
    const newScenes = [...story.scenes, {
      id: `scene${story.scenes.length + 1}`,
      background: '',
      bgm: '',
      nextScene: '',
      dialogues: [{ 
        character: '', 
        text: 'ここにセリフを入力します', 
        sound: '',
        backgroundChange: {
          enabled: false,
          background: '',
          fadeType: 'none',
          fadeDuration: 1000,
          removeBackground: false
        },
        bgmChange: {
          enabled: false,
          bgm: '',
          fadeType: 'none',
          fadeDuration: 1000,
          volume: 0.5,
          stopBgm: false
        },
        wait: {
          enabled: false,
          duration: 2000,
          autoProgress: false
        },
        fade: {
          enabled: false,
          type: 'in', 
          color: 'black',
          duration: 1000
        }
      }],
      choices: []
    }];
    
    setStory({...story, scenes: newScenes});
    setCurrentScene(newScenes.length - 1);
  };
  
  // セリフを追加
  const addDialogue = () => {
    const updatedScene = {...activeScene};
    updatedScene.dialogues = [
      ...updatedScene.dialogues,
      { 
        character: '', 
        text: '新しいセリフ', 
        sound: '',
        backgroundChange: {
          enabled: false,
          background: '',
          fadeType: 'none',
          fadeDuration: 1000,
          removeBackground: false
        },
        bgmChange: {
          enabled: false,
          bgm: '',
          fadeType: 'none',
          fadeDuration: 1000,
          volume: 0.5,
          stopBgm: false
        },
        wait: {
          enabled: false,
          duration: 2000,
          autoProgress: false
        },
        fade: {
          enabled: false,
          type: 'in',
          color: 'black',
          duration: 1000
        }
      }
    ];
    
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // 選択肢を追加
  const addChoice = () => {
    const updatedScene = {...activeScene};
    if (!updatedScene.choices) {
      updatedScene.choices = [];
    }
    
    updatedScene.choices.push({
      text: '選択肢',
      nextScene: ''
    });
    
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // キャラクターを追加
  const addCharacter = () => {
    setCharacters([
      ...characters,
      { id: `char${characters.length + 1}`, name: '新しいキャラクター', image: '' }
    ]);
  };
  
  // ダイアログを更新
  const updateDialogue = (index, field, value) => {
    const updatedScene = {...activeScene};
    updatedScene.dialogues[index] = {
      ...updatedScene.dialogues[index],
      [field]: value
    };
    
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // ダイアログの特殊効果を更新
  const updateDialogueEffect = (index, effectType, field, value) => {
    const updatedScene = {...activeScene};
    updatedScene.dialogues[index] = {
      ...updatedScene.dialogues[index],
      [effectType]: {
        ...updatedScene.dialogues[index][effectType],
        [field]: value
      }
    };
    
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // 選択肢を更新
  const updateChoice = (index, field, value) => {
    const updatedScene = {...activeScene};
    updatedScene.choices[index] = {
      ...updatedScene.choices[index],
      [field]: value
    };
    
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // キャラクターを更新
  const updateCharacter = (index, field, value) => {
    const updatedCharacters = [...characters];
    updatedCharacters[index] = {
      ...updatedCharacters[index],
      [field]: value
    };
    
    setCharacters(updatedCharacters);
  };
  
  // シーンの背景を設定
  const updateSceneBackground = (value) => {
    const updatedScene = {...activeScene, background: value};
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // シーンのBGMを設定
  const updateSceneBGM = (value) => {
    const updatedScene = {...activeScene, bgm: value};
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // シーンの自動遷移先を設定
  const updateSceneNextScene = (value) => {
    const updatedScene = {...activeScene, nextScene: value};
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // ストーリータイトルを更新
  const updateTitle = (value) => {
    setStory({...story, title: value});
  };

  // ダイアログを上に移動
  const moveDialogueUp = (sceneIndex, dialogueIndex) => {
    if (dialogueIndex <= 0) return; // 既に一番上なら何もしない
    
    const updatedScene = {...story.scenes[sceneIndex]};
    const dialogues = [...updatedScene.dialogues];
    
    // ダイアログを入れ替え
    [dialogues[dialogueIndex-1], dialogues[dialogueIndex]] = [dialogues[dialogueIndex], dialogues[dialogueIndex-1]];
    
    updatedScene.dialogues = dialogues;
    
    const newScenes = [...story.scenes];
    newScenes[sceneIndex] = updatedScene;
    setStory({...story, scenes: newScenes});
  };

  // ダイアログを下に移動
  const moveDialogueDown = (sceneIndex, dialogueIndex) => {
    const scene = story.scenes[sceneIndex];
    if (dialogueIndex >= scene.dialogues.length - 1) return; // 既に一番下なら何もしない
    
    const updatedScene = {...scene};
    const dialogues = [...updatedScene.dialogues];
    
    // ダイアログを入れ替え
    [dialogues[dialogueIndex], dialogues[dialogueIndex+1]] = [dialogues[dialogueIndex+1], dialogues[dialogueIndex]];
    
    updatedScene.dialogues = dialogues;
    
    const newScenes = [...story.scenes];
    newScenes[sceneIndex] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // オープニング設定を更新
  const updateOpening = (field, value) => {
    setStory({
      ...story,
      opening: {
        ...story.opening,
        [field]: value
      }
    });
  };
  
  // エンディング設定を更新
  const updateEnding = (field, value) => {
    setStory({
      ...story,
      ending: {
        ...story.ending,
        [field]: value
      }
    });
  };
  
  // シーンを削除
  const deleteScene = (index) => {
    if (story.scenes.length <= 1) {
      alert('最低1つのシーンが必要です');
      return;
    }
    
    const newScenes = [...story.scenes];
    newScenes.splice(index, 1);
    setStory({...story, scenes: newScenes});
    
    if (currentScene >= newScenes.length) {
      setCurrentScene(newScenes.length - 1);
    }
  };
  
  // ダイアログを削除
  const deleteDialogue = (index) => {
    if (activeScene.dialogues.length <= 1) {
      alert('最低1つのセリフが必要です');
      return;
    }
    
    const updatedScene = {...activeScene};
    updatedScene.dialogues.splice(index, 1);
    
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // 選択肢を削除
  const deleteChoice = (index) => {
    const updatedScene = {...activeScene};
    updatedScene.choices.splice(index, 1);
    
    const newScenes = [...story.scenes];
    newScenes[currentScene] = updatedScene;
    setStory({...story, scenes: newScenes});
  };
  
  // キャラクターを削除
  const deleteCharacter = (index) => {
    if (characters.length <= 1) {
      alert('最低1人のキャラクターが必要です');
      return;
    }
    
    const updatedCharacters = [...characters];
    updatedCharacters.splice(index, 1);
    setCharacters(updatedCharacters);
  };
  
  // 次のシーンに移動
  const nextScene = () => {
    if (currentScene < story.scenes.length - 1) {
      setCurrentScene(currentScene + 1);
    }
  };
  
  // 前のシーンに移動
  const prevScene = () => {
    if (currentScene > 0) {
      setCurrentScene(currentScene - 1);
    }
  };
  
  // プレビューモード切り替え
  const togglePreview = (startAt = 'opening') => {
    setPreviewStartAt(startAt);
    setPreviewMode(!previewMode);
  };
  
  // エクスポートオプションダイアログを表示
  const showExportOptions = () => {
    setExportOptions({
      ...exportOptions,
      showDialog: true
    });
  };
  
  // ストーリーを保存（ZIPファイルとして）
  const saveStory = async () => {
    try {
      // 相対パスに変換
      const { processedStory, processedCharacters } = processPathsForExport(story, characters, assets);
      
      // Zipファイルを作成
      const zip = new JSZip();
      
      // プロジェクトJSONを追加
      zip.file("project.json", JSON.stringify({
        story: processedStory,
        characters: processedCharacters,
        assetStructure: {
          version: "1.0",
          format: "asset/{category}/{subCategory}/{fileName}"
        }
      }));
      
      // アセットを追加
      await addAssetsToZip(zip, assets);
      
      // ZIPファイルを生成して保存
      zip.generateAsync({type: "blob"})
        .then(function(content) {
          saveAs(content, `${story.title}.zip`);
        });
    } catch (error) {
      console.error('保存エラー:', error);
      alert('プロジェクトの保存中にエラーが発生しました');
    }
  };
  
  // JSONプロジェクト読み込み
  const loadStory = async (e) => {
    const file = e.target.files[0];
    if (!file) return;
    
    if (file.name.endsWith('.zip')) {
      // ZIPファイルの場合
      const zip = new JSZip();
      try {
        const zipContent = await zip.loadAsync(file);
        
        // project.jsonを読み込む
        const projectJsonFile = zipContent.file("project.json");
        if (!projectJsonFile) {
          throw new Error("project.jsonが見つかりません");
        }
        
        const projectJson = await projectJsonFile.async("string");
        const projectData = JSON.parse(projectJson);
        
        // アセットを読み込む
        const newAssets = { 
          image: { bg: {}, character: {} },
          sound: { bgm: {}, se: {} }
        };
        
        // ZIPからアセットを抽出
        const assetPromises = [];
        const pathToUrlMap = {};
        
        zipContent.forEach((relativePath, zipEntry) => {
          if (zipEntry.dir) return; // ディレクトリはスキップ
          
          // assetフォルダ内のファイルのみ処理
          if (relativePath.startsWith('asset/')) {
            const pathParts = relativePath.split('/');
            if (pathParts.length !== 4) return; // 期待するパス構造と一致しない
            
            const category = pathParts[1];
            const subCategory = pathParts[2];
            const fileName = pathParts[3];
            
            // 対応するカテゴリとサブカテゴリを確認
            const validCategories = ['image', 'sound'];
            const validSubCategories = {
              'image': ['bg', 'character'],
              'sound': ['bgm', 'se']
            };
            
            if (!validCategories.includes(category) || 
                !validSubCategories[category].includes(subCategory)) {
              return; // 無効なカテゴリ/サブカテゴリはスキップ
            }
            
            // ファイルを読み込んでBlobURLを作成
            assetPromises.push(
              zipEntry.async("blob").then(blob => {
                // ファイルタイプを推測
                let fileType;
                if (category === 'image') {
                  fileType = 'image/' + (fileName.toLowerCase().endsWith('.png') ? 'png' : 'jpeg');
                } else {
                  fileType = 'audio/' + (fileName.toLowerCase().endsWith('.mp3') ? 'mpeg' : 'wav');
                }
                
                // BlobURLを作成
                const blobUrl = URL.createObjectURL(new Blob([blob], { type: fileType }));
                
                // アセットを追加
                if (!newAssets[category]) newAssets[category] = {};
                if (!newAssets[category][subCategory]) newAssets[category][subCategory] = {};
                
                newAssets[category][subCategory][fileName] = {
                  url: blobUrl,
                  type: fileType,
                  size: blob.size,
                  lastModified: new Date().getTime()
                };
                
                // 相対パスとBlobURLのマッピングを作成
                const assetPath = `asset/${category}/${subCategory}/${fileName}`;
                pathToUrlMap[assetPath] = blobUrl;
                
                return { assetPath, blobUrl };
              })
            );
          }
        });
        
        // すべてのアセットを読み込む
        await Promise.all(assetPromises);
        
        // ストーリーとキャラクターのパスをBlobURLに変換
        const convertPathToBlobUrl = (path) => {
          if (!path || path.startsWith('blob:')) return path;
          return pathToUrlMap[path] || path;
        };
        
        // ストーリーとキャラクターのパスを変換
        const processedStory = JSON.parse(JSON.stringify(projectData.story));
        const processedCharacters = JSON.parse(JSON.stringify(projectData.characters));
        
        // オープニング画面の画像パスを処理
        if (processedStory.opening) {
          if (processedStory.opening.background) {
            processedStory.opening.background = convertPathToBlobUrl(processedStory.opening.background);
          }
          if (processedStory.opening.bgm) {
            processedStory.opening.bgm = convertPathToBlobUrl(processedStory.opening.bgm);
          }
          if (processedStory.opening.logoImage) {
            processedStory.opening.logoImage = convertPathToBlobUrl(processedStory.opening.logoImage);
          }
        }
        
        // エンディング画面の処理
        if (processedStory.ending) {
          if (processedStory.ending.background) {
            processedStory.ending.background = convertPathToBlobUrl(processedStory.ending.background);
          }
          if (processedStory.ending.bgm) {
            processedStory.ending.bgm = convertPathToBlobUrl(processedStory.ending.bgm);
          }
        }
        
        // シーンの処理
        processedStory.scenes.forEach(scene => {
          if (scene.background) {
            scene.background = convertPathToBlobUrl(scene.background);
          }
          if (scene.bgm) {
            scene.bgm = convertPathToBlobUrl(scene.bgm);
          }
          
          // ダイアログの処理
          scene.dialogues.forEach(dialogue => {
            if (dialogue.sound) {
              dialogue.sound = convertPathToBlobUrl(dialogue.sound);
            }
            
            // 背景変更の処理
            if (dialogue.backgroundChange?.background) {
              dialogue.backgroundChange.background = convertPathToBlobUrl(dialogue.backgroundChange.background);
            }
            
            // BGM変更の処理
            if (dialogue.bgmChange?.bgm) {
              dialogue.bgmChange.bgm = convertPathToBlobUrl(dialogue.bgmChange.bgm);
            }
          });
        });
        
        // キャラクターの処理
        processedCharacters.forEach(character => {
          if (character.image) {
            character.image = convertPathToBlobUrl(character.image);
          }
        });
        
        // 読み込んだデータとアセットを設定
        setStory(processedStory);
        setCharacters(processedCharacters);
        setAssets(newAssets);
        setCurrentScene(0);
        
        // 読み込み完了音を再生（もし必要なら）
        if (newAssets.sound?.se && Object.keys(newAssets.sound.se).length > 0) {
          const firstSoundKey = Object.keys(newAssets.sound.se)[0];
          const audio = new Audio(newAssets.sound.se[firstSoundKey].url);
          audio.volume = 0.5;
          audio.play().catch(e => console.log('Sound autoplay prevented'));
        }
        
        alert('プロジェクトを正常に読み込みました');
      } catch (error) {
        console.error('ZIPファイルの読み込みエラー:', error);
        alert('ZIPファイルの読み込みに失敗しました: ' + error.message);
      }
    } else {
      // 通常のJSONファイルの場合（後方互換性のため）
      const reader = new FileReader();
      reader.onload = (event) => {
        try {
          const data = JSON.parse(event.target.result);
          
          // 古いバージョンとの互換性を確保
          if (data.story) {
            // シーンの新機能を確認
            data.story.scenes.forEach(scene => {
              // nextSceneプロパティがなければ追加
              if (!scene.hasOwnProperty('nextScene')) {
                scene.nextScene = '';
              }
              
              // ダイアログの新機能を確認
              scene.dialogues.forEach(dialogue => {
                // backgroundChange機能
                if (!dialogue.backgroundChange) {
                  dialogue.backgroundChange = {
                    enabled: false,
                    background: '',
                    fadeType: 'none',
                    fadeDuration: 1000,
                    removeBackground: false
                  };
                }
                
                // bgmChange機能
                if (!dialogue.bgmChange) {
                  dialogue.bgmChange = {
                    enabled: false,
                    bgm: '',
                    fadeType: 'none',
                    fadeDuration: 1000,
                    volume: 0.5,
                    stopBgm: false
                  };
                }
              });
            });
            
            // 古いバージョンとの互換性を確保
            if (!data.story.opening) {
              data.story.opening = {
                enabled: true,
                background: '',
                bgm: '',
                title: data.story.title || '',
                subtitle: '',
                titleColor: '#ffffff',
                subtitleColor: '#cccccc',
                startButtonText: 'はじめる',
                startButtonColor: '#ffffff',
                startButtonBgColor: '#333333',
                fadeInDuration: 2000,
                logoImage: ''
              };
            }
            
            if (!data.story.ending) {
              data.story.ending = {
                enabled: true,
                background: '',
                bgm: '',
                message: 'ゲームクリア！',
                messageColor: '#ffffff',
                credit: '制作：あなたの名前',
                creditColor: '#cccccc',
                returnButtonText: 'タイトルに戻る',
                returnButtonColor: '#ffffff',
                returnButtonBgColor: '#333333',
                fadeInDuration: 2000
              };
            }
            
            setStory(data.story);
            setCharacters(data.characters);
            setCurrentScene(0);
            
            alert('古い形式のプロジェクトを読み込みました。保存時に新形式に変換されます。');
          }
        } catch (error) {
          console.error('ファイルの読み込みエラー:', error);
          alert('ファイルの読み込みに失敗しました: ' + error.message);
        }
      };
      reader.readAsText(file);
    }
  };
  
  // HTML出力機能の修正版
  const exportAsHTML = async () => {
    setExportOptions({...exportOptions, showDialog: false});
    
    try {
      // 相対パスに変換
      const { processedStory, processedCharacters } = processPathsForExport(story, characters, assets);
      
      // HTMLテンプレートを生成
      const htmlTemplate = `
<!DOCTYPE html>
<html lang="ja">
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title>${story.title}</title>
    <style>
        body, html {
            margin: 0;
            padding: 0;
            width: 100%;
            height: 100%;
            font-family: sans-serif;
            overflow: hidden;
        }
        #novel-container {
            position: relative;
            width: 100%;
            height: 100vh;
            background-color: #000;
        }
        #background {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-size: cover;
            background-position: center;
            transition: background-image 0.5s ease;
        }
        #character {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            height: 80%;
            background-position: bottom center;
            background-repeat: no-repeat;
            background-size: contain;
        }
        #text-box {
            position: absolute;
            bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
            width: 90%;
            padding: 15px;
            background-color: rgba(0, 0, 0, 0.7);
            color: white;
            border-radius: 10px;
            z-index: 10;
        }
        #speaker {
            color: #ffeb3b;
            font-weight: bold;
            margin-bottom: 10px;
        }
        #text {
            line-height: 1.6;
            min-height: 4em;
        }
        #choices {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 80%;
            z-index: 20;
            display: none;
        }
        .choice-btn {
            display: block;
            width: 100%;
            margin-bottom: 10px;
            padding: 10px;
            background-color: rgba(0, 0, 0, 0.8);
            color: white;
            border: 1px solid #666;
            border-radius: 5px;
            text-align: left;
            cursor: pointer;
            transition: all 0.2s;
        }
        .choice-btn:hover {
            background-color: rgba(50, 50, 50, 0.8);
        }
        .next-icon {
            position: absolute;
            bottom: 10px;
            right: 10px;
            width: 30px;
            height: 30px;
            animation: bounce 1s infinite;
        }
        @keyframes bounce {
            0%, 100% { transform: translateY(0); }
            50% { transform: translateY(-10px); }
        }
        .fade-overlay {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: black;
            opacity: 0;
            transition: opacity 0.5s ease;
            pointer-events: none;
            z-index: 30;
        }
        
        /* オープニング画面のスタイル */
        #opening-screen {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            background-color: #000;
            background-size: cover;
            background-position: center;
            color: white;
            z-index: 40;
        }
        #opening-logo {
            max-width: 80%;
            max-height: 40%;
            margin-bottom: 20px;
        }
        #opening-title {
            font-size: 3rem;
            font-weight: bold;
            margin-bottom: 10px;
            text-align: center;
            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
        }
        #opening-subtitle {
            font-size: 1.5rem;
            margin-bottom: 40px;
            text-align: center;
            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
        }
        #start-button {
            padding: 12px 30px;
            font-size: 1.2rem;
            border: none;
            border-radius: 5px;
            cursor: pointer;
            transition: all 0.2s;
        }
        #start-button:hover {
            opacity: 0.8;
        }
        
        /* エンディング画面のスタイル */
        #ending-screen {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            background-color: #000;
            background-size: cover;
            background-position: center;
            color: white;
            z-index: 40;
            display: none;
        }
        #ending-message {
            font-size: 3rem;
            font-weight: bold;
            margin-bottom: 20px;
            text-align: center;
            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
        }
        #ending-credit {
            font-size: 1.5rem;
            margin-bottom: 40px;
            text-align: center;
            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
        }
        #return-button {
            padding: 12px 30px;
            font-size: 1.2rem;
            border: none;
            border-radius: 5px;
            cursor: pointer;
            transition: all 0.2s;
        }
        #return-button:hover {
            opacity: 0.8;
        }
    </style>
</head>
<body>
    <div id="novel-container">
        <!-- オープニング画面 -->
        <div id="opening-screen">
            <img id="opening-logo" src="" alt="" style="display: none;">
            <div id="opening-title"></div>
            <div id="opening-subtitle"></div>
            <button id="start-button"></button>
        </div>
        
        <!-- ゲーム本編 -->
        <div id="background"></div>
        <div id="character"></div>
        <div id="text-box">
            <div id="speaker"></div>
            <div id="text"></div>
            <div class="next-icon">▼</div>
        </div>
        <div id="choices"></div>
        <div class="fade-overlay"></div>
        
        <!-- エンディング画面 -->
        <div id="ending-screen">
            <div id="ending-message"></div>
            <div id="ending-credit"></div>
            <button id="return-button"></button>
        </div>
    </div>

    <audio id="bgm-player" loop></audio>
    <audio id="se-player"></audio>

    <script>
    // ストーリーデータ
    const story = ${JSON.stringify(processedStory)};
    const characters = ${JSON.stringify(processedCharacters)};
    
    // 状態変数
    let currentSceneIndex = 0;
    let currentDialogueIndex = 0;
    let isTyping = false;
    let textProgress = '';
    let typingInterval;
    let showingChoices = false;
    let waitTimer = null;
    let fadeTimer = null;
    let isFading = false;
    let gameState = 'opening'; // 'opening', 'story', 'ending'
    
    // DOM要素
    const container = document.getElementById('novel-container');
    const background = document.getElementById('background');
    const character = document.getElementById('character');
    const textBox = document.getElementById('text-box');
    const speaker = document.getElementById('speaker');
    const text = document.getElementById('text');
    const choices = document.getElementById('choices');
    const fadeOverlay = document.querySelector('.fade-overlay');
    const bgmPlayer = document.getElementById('bgm-player');
    const sePlayer = document.getElementById('se-player');
    const nextIcon = document.querySelector('.next-icon');
    
    // オープニング要素
    const openingScreen = document.getElementById('opening-screen');
    const openingLogo = document.getElementById('opening-logo');
    const openingTitle = document.getElementById('opening-title');
    const openingSubtitle = document.getElementById('opening-subtitle');
    const startButton = document.getElementById('start-button');
    
    // エンディング要素
    const endingScreen = document.getElementById('ending-screen');
    const endingMessage = document.getElementById('ending-message');
    const endingCredit = document.getElementById('ending-credit');
    const returnButton = document.getElementById('return-button');
    
    // 初期化
    window.onload = function() {
        // オープニング設定
        if (story.opening.enabled) {
            setupOpening();
            showOpening();
        } else {
            hideOpening();
            startStory();
        }
        
        // クリックイベント
        container.addEventListener('click', handleClick);
        
        // スタートボタンイベント
        startButton.addEventListener('click', function(e) {
            e.stopPropagation();
            hideOpening();
            startStory();
        });
        
        // 戻るボタンイベント
        returnButton.addEventListener('click', function(e) {
            e.stopPropagation();
            hideEnding();
            showOpening();
        });
    };
    
    // オープニング設定
    function setupOpening() {
        // 背景設定
        if (story.opening.background) {
            openingScreen.style.backgroundImage = \`url("\${story.opening.background}")\`;
        }
        
        // ロゴ設定
        if (story.opening.logoImage) {
            openingLogo.src = story.opening.logoImage;
            openingLogo.style.display = 'block';
        } else {
            openingLogo.style.display = 'none';
        }
        
        // タイトル設定
        openingTitle.textContent = story.opening.title || story.title;
        openingTitle.style.color = story.opening.titleColor;
        
        // サブタイトル設定
        openingSubtitle.textContent = story.opening.subtitle;
        openingSubtitle.style.color = story.opening.subtitleColor;
        
        // スタートボタン設定
        startButton.textContent = story.opening.startButtonText;
        startButton.style.color = story.opening.startButtonColor;
        startButton.style.backgroundColor = story.opening.startButtonBgColor;
        
        // BGM設定
        if (story.opening.bgm) {
            bgmPlayer.src = story.opening.bgm;
        }
    }
    
    // エンディング設定
    function setupEnding() {
        // 背景設定
        if (story.ending.background) {
            endingScreen.style.backgroundImage = \`url("\${story.ending.background}")\`;
        }
        
        // メッセージ設定
        endingMessage.textContent = story.ending.message;
        endingMessage.style.color = story.ending.messageColor;
        
        // クレジット設定
        endingCredit.textContent = story.ending.credit;
        endingCredit.style.color = story.ending.creditColor;
        
        // 戻るボタン設定
        returnButton.textContent = story.ending.returnButtonText;
        returnButton.style.color = story.ending.returnButtonColor;
        returnButton.style.backgroundColor = story.ending.returnButtonBgColor;
        
        // BGM設定
        if (story.ending.bgm) {
            bgmPlayer.src = story.ending.bgm;
        }
    }
    
    // オープニングを表示
    function showOpening() {
        gameState = 'opening';
        
        // BGM再生
        if (story.opening.bgm) {
            bgmPlayer.play().catch(e => console.log('BGM autoplay prevented'));
        }
        
        // オープニング画面フェードイン
        openingScreen.style.opacity = '0';
        openingScreen.style.display = 'flex';
        
        setTimeout(() => {
            openingScreen.style.transition = \`opacity \${story.opening.fadeInDuration}ms ease\`;
            openingScreen.style.opacity = '1';
        }, 10);
    }
    
    // オープニングを非表示
    function hideOpening() {
        // フェードアウト
        openingScreen.style.opacity = '0';
        
        setTimeout(() => {
            openingScreen.style.display = 'none';
        }, 1000);
    }
    
    // エンディングを表示
    function showEnding() {
        gameState = 'ending';
        
        // フェード状態をリセット
        fadeOverlay.style.opacity = '0';
        isFading = false;
        
        // タイマーをクリア
        clearAllTimers();
        
        // エンディング設定
        setupEnding();
        
        // BGM停止
        bgmPlayer.pause();
        
        // BGM再生
        if (story.ending.bgm) {
            bgmPlayer.play().catch(e => console.log('BGM autoplay prevented'));
        }
        
        // エンディング画面フェードイン
        endingScreen.style.opacity = '0';
        endingScreen.style.display = 'flex';
        
        setTimeout(() => {
            endingScreen.style.transition = \`opacity \${story.ending.fadeInDuration}ms ease\`;
            endingScreen.style.opacity = '1';
        }, 10);
    }
    
    // エンディングを非表示
    function hideEnding() {
        // フェードアウト
        endingScreen.style.opacity = '0';
        
        setTimeout(() => {
            endingScreen.style.display = 'none';
        }, 1000);
    }
    
    // ストーリーを開始
    function startStory() {
        gameState = 'story';
        startScene(0);
    }
    
    // シーン開始
    function startScene(sceneIndex) {
        const scene = story.scenes[sceneIndex];
        currentSceneIndex = sceneIndex;
        currentDialogueIndex = 0;
        showingChoices = false;
        
        // 前のタイマーをクリア
        clearAllTimers();
        
        setupScene(scene);
        startDialogue();
    }
    
    // タイマーをクリア
    function clearAllTimers() {
        clearTimeout(waitTimer);
        clearTimeout(fadeTimer);
        clearInterval(typingInterval);
        waitTimer = null;
        fadeTimer = null;
        typingInterval = null;
    }
    
    // シーン設定
    function setupScene(scene) {
        // 背景設定
        background.style.backgroundImage = scene.background ? \`url("\${scene.background}")\` : '';
        
        // BGM設定
        if (scene.bgm) {
            bgmPlayer.src = scene.bgm;
            bgmPlayer.volume = 0.5;
            bgmPlayer.play().catch(e => console.log('BGM autoplay prevented by browser policy'));
        } else {
            bgmPlayer.pause();
        }
    }
    
    // 背景の変更処理
    async function changeBackground(bgSettings) {
        if (!bgSettings || !bgSettings.enabled) return;
        
        // 背景を非表示にする場合
        if (bgSettings.removeBackground) {
            // フェードが必要な場合はフェードアウト
            if (bgSettings.fadeType !== 'none') {
                await applyFadeOut('black', bgSettings.fadeDuration);
            }
            
            background.style.backgroundImage = 'none';
            background.style.backgroundColor = '#000000';
            
            // フェードインが必要な場合
            if (bgSettings.fadeType !== 'none') {
                await applyFadeIn('black', bgSettings.fadeDuration);
            }
            return;
        }
        
        // 新しい背景がなければ何もしない
        if (!bgSettings.background) return;
        
        // フェードタイプに応じた処理
        if (bgSettings.fadeType === 'fade') {
            // フェードアウト → 背景変更 → フェードイン
            await applyFadeOut('black', bgSettings.fadeDuration / 2);
            background.style.backgroundImage = \`url("\${bgSettings.background}")\`;
            await applyFadeIn('black', bgSettings.fadeDuration / 2);
        } else if (bgSettings.fadeType === 'crossfade') {
            // クロスフェード処理
            const oldBg = background.style.backgroundImage;
            
            // 一時的な背景要素を作成
            const tempBg = document.createElement('div');
            tempBg.style.position = 'absolute';
            tempBg.style.top = '0';
            tempBg.style.left = '0';
            tempBg.style.width = '100%';
            tempBg.style.height = '100%';
            tempBg.style.backgroundSize = 'cover';
            tempBg.style.backgroundPosition = 'center';
            tempBg.style.backgroundImage = oldBg;
            tempBg.style.opacity = '1';
            tempBg.style.transition = \`opacity \${bgSettings.fadeDuration}ms ease\`;
            container.insertBefore(tempBg, background);
            
            // 新しい背景を設定
            background.style.backgroundImage = \`url("\${bgSettings.background}")\`;
            background.style.opacity = '0';
            background.style.transition = \`opacity \${bgSettings.fadeDuration}ms ease\`;
            
            // フェード実行
            setTimeout(() => {
                background.style.opacity = '1';
                tempBg.style.opacity = '0';
                setTimeout(() => {
                    container.removeChild(tempBg);
                }, bgSettings.fadeDuration);
            }, 10);
            
            // クロスフェード完了を待機
            await new Promise(resolve => setTimeout(resolve, bgSettings.fadeDuration + 100));
        } else {
            // 直接切り替え
            background.style.backgroundImage = \`url("\${bgSettings.background}")\`;
        }
    }
    
    // BGMの変更処理
    function changeBGM(bgmSettings) {
        if (!bgmSettings || !bgmSettings.enabled) return;
        
        // BGMを停止する場合
        if (bgmSettings.stopBgm) {
            if (bgmPlayer) {
                bgmPlayer.pause();
                bgmPlayer.src = '';
            }
            return;
        }
        
        // 新しいBGMがなければ何もしない
        if (!bgmSettings.bgm) return;
        
        // フェードタイプに応じた処理
        if (bgmSettings.fadeType === 'crossfade') {
            // 現在のBGMをフェードアウトしながら新しいBGMをフェードイン
            const newBgm = new Audio(bgmSettings.bgm);
            newBgm.loop = true;
            newBgm.volume = 0;
            
            const fadeStep = 0.05;
            const fadeInterval = bgmSettings.fadeDuration / (1 / fadeStep);
            
            // 新しいBGMを再生開始
            const playPromise = newBgm.play();
            if (playPromise !== undefined) {
                playPromise.catch(e => console.log('BGM autoplay prevented by browser policy', e));
            }
            
            // クロスフェード処理
            let volume = 0;
            const intervalId = setInterval(() => {
                volume += fadeStep;
                if (volume >= bgmSettings.volume) {
                    volume = bgmSettings.volume;
                    clearInterval(intervalId);
                    
                    // 古いBGMを停止して新しいBGMに置き換え
                    bgmPlayer.pause();
                    bgmPlayer.src = bgmSettings.bgm;
                    bgmPlayer.volume = bgmSettings.volume;
                    bgmPlayer.play().catch(e => console.log('BGM autoplay prevented by browser policy', e));
                    
                    // 一時的なBGM要素を破棄
                    newBgm.pause();
                    newBgm.src = '';
                }
                
                // ボリューム調整
                bgmPlayer.volume = bgmSettings.volume - volume;
                newBgm.volume = volume;
            }, fadeInterval);
        } else {
            // 直接切り替え
            bgmPlayer.src = bgmSettings.bgm;
            bgmPlayer.volume = bgmSettings.volume;
            bgmPlayer.play().catch(e => console.log('BGM autoplay prevented by browser policy', e));
        }
    }
    
    // ダイアログ開始
    async function startDialogue() {
        const scene = story.scenes[currentSceneIndex];
        if (!scene.dialogues || currentDialogueIndex >= scene.dialogues.length) {
            showChoices();
            return;
        }
        
        const dialogue = scene.dialogues[currentDialogueIndex];
        
        // フェードイン効果を適用
        if (dialogue.fade && dialogue.fade.enabled && dialogue.fade.type === 'in') {
            await applyFadeIn(dialogue.fade.color, dialogue.fade.duration);
        }
        
        // 背景の変更を適用
        if (dialogue.backgroundChange && dialogue.backgroundChange.enabled) {
            await changeBackground(dialogue.backgroundChange);
        }
        
        // BGMの変更を適用
        if (dialogue.bgmChange && dialogue.bgmChange.enabled) {
            changeBGM(dialogue.bgmChange);
        }
        
        // キャラクター表示
        if (dialogue.character) {
            const char = characters.find(c => c.name === dialogue.character);
            speaker.textContent = dialogue.character;
            if (char && char.image) {
                character.style.backgroundImage = \`url("\${char.image}")\`;
                character.style.display = 'block';
            } else {
                character.style.display = 'none';
            }
        } else {
            speaker.textContent = '';
            character.style.display = 'none';
        }
        
        // 効果音再生
        if (dialogue.sound) {
            sePlayer.src = dialogue.sound;
            sePlayer.volume = 0.7;
            sePlayer.play().catch(e => console.log('SE autoplay prevented'));
        }
        
        // テキスト表示（タイプライター効果）
        text.textContent = '';
        textProgress = '';
        isTyping = true;
        nextIcon.style.display = 'none';
        
        let i = 0;
        clearInterval(typingInterval);
        typingInterval = setInterval(() => {
            if (i < dialogue.text.length) {
                textProgress += dialogue.text.charAt(i);
                text.textContent = textProgress;
                i++;
            } else {
                clearInterval(typingInterval);
                isTyping = false;
                nextIcon.style.display = 'block';
                
                // ウェイト設定があれば実行
                if (dialogue.wait && dialogue.wait.enabled) {
                    const waitDuration = dialogue.wait.duration || 2000;
                    waitTimer = setTimeout(() => {
                        if (dialogue.wait.autoProgress) {
                            // 自動的に次のダイアログへ
                            nextDialogue();
                        }
                    }, waitDuration);
                }
                
                // フェードアウト効果を適用
                if (dialogue.fade && dialogue.fade.enabled && dialogue.fade.type === 'out') {
                    waitTimer = setTimeout(() => {
                        applyFadeOut(dialogue.fade.color, dialogue.fade.duration).then(() => {
                            nextDialogue();
                        });
                    }, dialogue.wait && dialogue.wait.enabled ? dialogue.wait.duration : 1000);
                }
            }
        }, 50);
    }
    
    // フェードイン効果
    function applyFadeIn(color = 'black', duration = 1000) {
        return new Promise((resolve) => {
            isFading = true;
            fadeOverlay.style.backgroundColor = color;
            fadeOverlay.style.opacity = '1';
            fadeOverlay.style.transition = \`opacity \${duration}ms ease\`;
            
            fadeTimer = setTimeout(() => {
                fadeOverlay.style.opacity = '0';
                fadeTimer = setTimeout(() => {
                    isFading = false;
                    resolve();
                }, duration);
            }, 100);
        });
    }
    
    // フェードアウト効果
    function applyFadeOut(color = 'black', duration = 1000) {
        return new Promise((resolve) => {
            isFading = true;
            fadeOverlay.style.backgroundColor = color;
            fadeOverlay.style.opacity = '0';
            fadeOverlay.style.transition = \`opacity \${duration}ms ease\`;
            
            fadeTimer = setTimeout(() => {
                fadeOverlay.style.opacity = '1';
                fadeTimer = setTimeout(() => {
                    isFading = false;
                    resolve();
                }, duration);
            }, 100);
        });
    }
    
    // 選択肢表示
    function showChoices() {
        const scene = story.scenes[currentSceneIndex];
        
        if (!scene.choices || scene.choices.length === 0) {
            // 選択肢がない場合
            
            // 次のシーンが指定されている場合は自動的に遷移
            if (scene.nextScene) {
                const nextSceneIndex = story.scenes.findIndex(s => s.id === scene.nextScene);
                if (nextSceneIndex !== -1) {
                    startScene(nextSceneIndex);
                    return;
                }
            }
            
            // エンディングへ移行またはシーン遷移
            if (currentSceneIndex === story.scenes.length - 1 && story.ending.enabled) {
                // フェード中なら完了を待つ
                if (isFading) {
                    // フェードが完了してからエンディングへ
                    fadeTimer = setTimeout(() => {
                        isFading = false;
                        fadeOverlay.style.opacity = '0';
                        showEnding();
                    }, 1000); // フェード完了を待つ適当な時間
                    return;
                }
                
                // 最後のシーンが終わったらエンディングへ
                showEnding();
                return;
            }
            
            // 次のシーンがない場合は何もしない
            return;
        }
        
        // 選択肢を表示
        choices.innerHTML = '';
        showingChoices = true;
        
        scene.choices.forEach(choice => {
            const button = document.createElement('button');
            button.className = 'choice-btn';
            button.textContent = choice.text;
            button.addEventListener('click', () => {
                const nextSceneIndex = story.scenes.findIndex(s => s.id === choice.nextScene);
                if (nextSceneIndex !== -1) {
                    startScene(nextSceneIndex);
                }
            });
            choices.appendChild(button);
        });
        
        choices.style.display = 'block';
    }
    
    // クリック処理
    function handleClick(e) {
        // オープニング・エンディング中は無視
        if (gameState !== 'story') return;
        
        // フェード中は無視
        if (isFading) return;
        
        // 選択肢表示中は無視
        if (showingChoices) return;
        
        // 選択肢ボタンのクリックは伝播させない
        if (e.target.classList.contains('choice-btn')) {
            e.stopPropagation();
            return;
        }
        
        // タイピング中ならスキップ
        if (isTyping) {
            clearInterval(typingInterval);
            const scene = story.scenes[currentSceneIndex];
            const dialogue = scene.dialogues[currentDialogueIndex];
            text.textContent = dialogue.text;
            isTyping = false;
            nextIcon.style.display = 'block';
            return;
        }
        
        // ウェイト中のタイマーをクリア
        clearTimeout(waitTimer);
        
        // 次のダイアログへ
        nextDialogue();
    }
    
    // 次のダイアログへ
    function nextDialogue() {
        const scene = story.scenes[currentSceneIndex];
        
        // 最後のダイアログだった場合は選択肢を表示
        if (currentDialogueIndex >= scene.dialogues.length - 1) {
            currentDialogueIndex++;
            showChoices();
            return;
        }
        
        // 次のダイアログへ
        currentDialogueIndex++;
        startDialogue();
    }
    </script>
</body>
</html>
      `;
      
      // ZIPファイルを作成
      const zip = new JSZip();
      
      // HTMLファイルを追加
      zip.file(`${story.title}.html`, htmlTemplate);
      
      // アセットフォルダを作成
      const assetFolder = zip.folder("asset");
      
      // アセットを追加
      await addAssetsToZip(zip, assets);
      
      // ZIPファイルを生成して保存
      zip.generateAsync({type: "blob"})
        .then(function(content) {
          saveAs(content, `${story.title}_game.zip`);
          
          // エクスポート後のインストラクション
          alert(
            `出力が完了しました。以下の手順に従ってください：\n\n` +
            `1. ZIPファイルを解凍してください。\n` +
            `2. HTMLファイルを開いてゲームを実行できます。\n\n` +
            `※注意: ネットワークエラーが表示される場合は、ローカルサーバーを使用するか、ブラウザのセキュリティ設定を変更してください。`
          );
        });
    } catch (error) {
      console.error('エクスポートエラー:', error);
      alert('HTMLのエクスポート中にエラーが発生しました: ' + error.message);
    }
  };

  // メインUI
  return (
    <div className="flex flex-col h-screen bg-gray-100">
      {/* ヘッダー */}
      <header className="bg-blue-600 text-white p-4 flex justify-between items-center">
        <h1 className="text-xl font-bold">サウンドノベルメーカー</h1>
        <div className="flex space-x-3">
          <button 
            className="flex items-center px-3 py-1 bg-blue-500 rounded hover:bg-blue-400"
            onClick={saveStory}
          >
            <Save size={16} className="mr-1" /> 保存
          </button>
          <label className="flex items-center px-3 py-1 bg-blue-500 rounded hover:bg-blue-400 cursor-pointer">
            <Upload size={16} className="mr-1" /> 読み込み
            <input 
              type="file" 
              className="hidden" 
              accept=".json,.zip" 
              onChange={loadStory}
            />
          </label>
          <button 
            className="flex items-center px-3 py-1 bg-purple-500 rounded hover:bg-purple-400"
            onClick={showExportOptions}
          >
            <Download size={16} className="mr-1" /> 実行ファイル出力
          </button>
          <div className="flex space-x-1">
            <button 
              className="flex items-center px-3 py-1 bg-green-500 rounded-l hover:bg-green-400"
              onClick={() => togglePreview('opening')}
              title="オープニングからプレビュー"
            >
              <Play size={16} className="mr-1" /> OP
            </button>
            <button 
              className="flex items-center px-3 py-1 bg-green-500 hover:bg-green-400"
              onClick={() => togglePreview('story')}
              title="ストーリーからプレビュー"
            >
              <Play size={16} className="mr-1" /> 本編
            </button>
            <button 
              className="flex items-center px-3 py-1 bg-green-500 rounded-r hover:bg-green-400"
              onClick={() => togglePreview('ending')}
              title="エンディングからプレビュー"
            >
              <Play size={16} className="mr-1" /> ED
            </button>
          </div>
        </div>
      </header>
      
      {/* エクスポートオプションダイアログ */}
      {exportOptions.showDialog && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 w-96">
            <h2 className="text-xl font-bold mb-4">出力設定</h2>
            
            <div className="mb-4">
              <p className="text-sm text-gray-600 mb-4">
                ゲームファイルをZIPアーカイブとして出力します。
                アセットフォルダも含めて出力されます。
              </p>
            </div>
            
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">
                アセットフォルダ名
              </label>
              <input 
                type="text"
                value={exportOptions.assetsFolder}
                onChange={(e) => setExportOptions({...exportOptions, assetsFolder: e.target.value})}
                className="w-full p-2 border rounded"
              />
            </div>
            
            <div className="flex justify-end space-x-2">
              <button 
                className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400"
                onClick={() => setExportOptions({...exportOptions, showDialog: false})}
              >
                キャンセル
              </button>
              <button 
                className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
                onClick={exportAsHTML}
              >
                出力
              </button>
            </div>
          </div>
        </div>
      )}
      
      {/* プレビューモード */}
      {previewMode ? (
        <NovelPreview 
          story={story} 
          characters={characters} 
          initialScene={currentScene}
          startAt={previewStartAt}
          onClose={() => setPreviewMode(false)} 
        />
      ) : (
        <div className="flex flex-1 overflow-hidden">
          {/* サイドバー */}
          <div className="w-16 bg-gray-800 text-white flex flex-col items-center py-4">
            <button 
              className={`p-2 rounded mb-4 ${activeTab === 'opening' ? 'bg-blue-500' : 'bg-gray-700'}`}
              onClick={() => setActiveTab('opening')}
              title="オープニング設定"
            >
              <Home size={24} />
            </button>
            <button 
              className={`p-2 rounded mb-4 ${activeTab === 'story' ? 'bg-blue-500' : 'bg-gray-700'}`}
              onClick={() => setActiveTab('story')}
              title="ストーリー"
            >
              <Book size={24} />
            </button>
            <button 
              className={`p-2 rounded mb-4 ${activeTab === 'characters' ? 'bg-blue-500' : 'bg-gray-700'}`}
              onClick={() => setActiveTab('characters')}
              title="キャラクター"
            >
              <Users size={24} />
            </button>
            <button 
              className={`p-2 rounded mb-4 ${activeTab === 'ending' ? 'bg-blue-500' : 'bg-gray-700'}`}
              onClick={() => setActiveTab('ending')}
              title="エンディング設定"
            >
              <Flag size={24} />
            </button>
            <button 
              className={`p-2 rounded mb-4 ${activeTab === 'assets' ? 'bg-blue-500' : 'bg-gray-700'}`}
              onClick={() => setActiveTab('assets')}
              title="アセット管理"
            >
              <FileText size={24} />
            </button>
          </div>
          
          {/* メインコンテンツ */}
          <div className="flex-1 overflow-auto p-4">
            {activeTab === 'opening' && (
              <OpeningEditor 
                opening={story.opening}
                updateOpening={updateOpening}
                handleFileSelect={handleFileSelect}
                assets={assets}
              />
            )}
            
            {activeTab === 'story' && (
              <StoryEditor 
                story={story}
                currentScene={currentScene}
                characters={characters}
                updateTitle={updateTitle}
                updateSceneBackground={updateSceneBackground}
                updateSceneBGM={updateSceneBGM}
                updateSceneNextScene={updateSceneNextScene}
                updateDialogue={updateDialogue}
                updateDialogueEffect={updateDialogueEffect}
                updateChoice={updateChoice}
                addDialogue={addDialogue}
                addChoice={addChoice}
                deleteDialogue={deleteDialogue}
                deleteChoice={deleteChoice}
                addScene={addScene}
                deleteScene={deleteScene}
                prevScene={prevScene}
                nextScene={nextScene}
                handleFileSelect={handleFileSelect}
                moveDialogueUp={moveDialogueUp}
                moveDialogueDown={moveDialogueDown}
                assets={assets}
              />
            )}
            
            {activeTab === 'characters' && (
              <CharacterEditor 
                characters={characters}
                updateCharacter={updateCharacter}
                addCharacter={addCharacter}
                deleteCharacter={deleteCharacter}
                handleFileSelect={handleFileSelect}
                assets={assets}
              />
            )}
            
            {activeTab === 'ending' && (
              <EndingEditor 
                ending={story.ending}
                updateEnding={updateEnding}
                handleFileSelect={handleFileSelect}
                assets={assets}
              />
            )}
            
            {activeTab === 'assets' && (
              <AssetManager 
                assets={assets}
                setAssets={setAssets}
              />
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default NovelMaker;