// src/utils/exportUtils.js

/**
 * 相対パスを取得
 * @param {string} blobUrl BlobURL
 * @param {object} assets アセット一覧
 * @returns {string} 相対パス
 */
export const getAssetPath = (blobUrl, assets) => {
    if (!blobUrl || !blobUrl.startsWith('blob:')) return blobUrl;
    
    // アセット一覧から検索
    for (const category in assets) {
      for (const subCategory in assets[category]) {
        for (const fileName in assets[category][subCategory]) {
          const fileData = assets[category][subCategory][fileName];
          if (fileData.url === blobUrl) {
            return `asset/${category}/${subCategory}/${fileName}`;
          }
        }
      }
    }
    
    return blobUrl; // 見つからなければそのまま
  };
  
  /**
   * ストーリーデータのパスを相対パスに変換する
   * @param {object} story ストーリーデータ
   * @param {array} characters キャラクターデータ
   * @param {object} assets アセット一覧
   * @returns {object} 処理済みのストーリーデータ
   */
  export const processPathsForExport = (story, characters, assets) => {
    const processedStory = JSON.parse(JSON.stringify(story));
    const processedCharacters = JSON.parse(JSON.stringify(characters));
    
    // オープニング画面の画像パスを処理
    if (processedStory.opening.background) {
      processedStory.opening.background = getAssetPath(processedStory.opening.background, assets);
    }
    if (processedStory.opening.bgm) {
      processedStory.opening.bgm = getAssetPath(processedStory.opening.bgm, assets);
    }
    if (processedStory.opening.logoImage) {
      processedStory.opening.logoImage = getAssetPath(processedStory.opening.logoImage, assets);
    }
    
    // エンディング画面の画像パスを処理
    if (processedStory.ending.background) {
      processedStory.ending.background = getAssetPath(processedStory.ending.background, assets);
    }
    if (processedStory.ending.bgm) {
      processedStory.ending.bgm = getAssetPath(processedStory.ending.bgm, assets);
    }
    
    // シーンの背景とBGMのパスを処理
    processedStory.scenes.forEach(scene => {
      if (scene.background) {
        scene.background = getAssetPath(scene.background, assets);
      }
      
      if (scene.bgm) {
        scene.bgm = getAssetPath(scene.bgm, assets);
      }
      
      // ダイアログの効果音と背景変更とBGM変更を処理
      scene.dialogues.forEach(dialogue => {
        if (dialogue.sound) {
          dialogue.sound = getAssetPath(dialogue.sound, assets);
        }
        
        // 背景変更の処理
        if (dialogue.backgroundChange?.background) {
          dialogue.backgroundChange.background = getAssetPath(dialogue.backgroundChange.background, assets);
        }
        
        // BGM変更の処理
        if (dialogue.bgmChange?.bgm) {
          dialogue.bgmChange.bgm = getAssetPath(dialogue.bgmChange.bgm, assets);
        }
      });
    });
    
    // キャラクター画像のパスを処理
    processedCharacters.forEach(character => {
      if (character.image) {
        character.image = getAssetPath(character.image, assets);
      }
    });
    
    return { processedStory, processedCharacters };
  };
  
  /**
   * HTMLエクスポート用にアセットをZIPファイルに追加
   * @param {object} zip JSZipインスタンス
   * @param {object} assets アセット一覧
   */
  export const addAssetsToZip = async (zip, assets) => {
    // アセットフォルダ構造を作成
    for (const category in assets) {
      for (const subCategory in assets[category]) {
        const folderPath = `asset/${category}/${subCategory}`;
        const folder = zip.folder(folderPath);
        
        for (const fileName in assets[category][subCategory]) {
          const fileData = assets[category][subCategory][fileName];
          
          try {
            // BlobURLからデータを取得
            const response = await fetch(fileData.url);
            const blob = await response.blob();
            
            // ZIPに追加
            folder.file(fileName, blob);
          } catch (error) {
            console.error(`ファイル追加エラー: ${fileName}`, error);
          }
        }
      }
    }
  };