// src/components/AssetManager.js

import React, { useState, useEffect, useRef } from 'react';
import { FolderOpen, Save, Edit, Trash2, Play, Square, Music, Image as ImageIcon } from 'lucide-react';

const AssetManager = ({ assets, setAssets }) => {
  const folderInputRef = useRef(null);
  const [selectedAsset, setSelectedAsset] = useState(null);
  const [newFileName, setNewFileName] = useState('');
  const [showRenameModal, setShowRenameModal] = useState(false);
  const [playingAudio, setPlayingAudio] = useState(null);
  const audioRef = useRef(null);
  
  // 再生中の音声があれば停止
  useEffect(() => {
    return () => {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current.src = '';
      }
    };
  }, []);
  
  // フォルダからアセットを一括読み込み
  const handleFolderSelect = async (e) => {
    // webkitdirectory属性を使用してフォルダ選択
    const files = e.target.files;
    if (!files || files.length === 0) return;
    
    const newAssets = { ...assets };
    
    // ファイル構造を解析
    Array.from(files).forEach(file => {
      // ファイルパスからカテゴリを抽出
      const path = file.webkitRelativePath;
      const pathParts = path.split('/');
      
      // ルートフォルダ名を除外
      if (pathParts.length < 3) return;
      
      // カテゴリとサブカテゴリを抽出
      // 例: "assets/image/bg/background.jpg" -> category="image", subCategory="bg"
      const category = pathParts[1]; 
      const subCategory = pathParts[2];
      const fileName = pathParts[pathParts.length - 1];
      
      // 対応するカテゴリとサブカテゴリを確認
      const validCategories = ['image', 'sound'];
      const validSubCategories = {
        'image': ['bg', 'character'],
        'sound': ['bgm', 'se']
      };
      
      if (!validCategories.includes(category) || 
          !validSubCategories[category].includes(subCategory)) {
        return; // 無効なカテゴリ/サブカテゴリはスキップ
      }
      
      // ファイルタイプをチェック
      const isValidType = (category === 'image' && file.type.startsWith('image/')) ||
                         (category === 'sound' && file.type.startsWith('audio/'));
      
      if (!isValidType) return;
      
      // blobURLを作成
      const blobUrl = URL.createObjectURL(file);
      
      // アセットを追加
      if (!newAssets[category]) newAssets[category] = {};
      if (!newAssets[category][subCategory]) newAssets[category][subCategory] = {};
      
      newAssets[category][subCategory][fileName] = {
        url: blobUrl,
        type: file.type,
        size: file.size,
        lastModified: file.lastModified,
        originalPath: path
      };
    });
    
    setAssets(newAssets);
    
    // 入力をリセット
    if (folderInputRef.current) {
      folderInputRef.current.value = '';
    }
  };
  
  // 音声ファイルのプレビュー
  const toggleAudioPreview = (assetUrl) => {
    if (playingAudio === assetUrl) {
      // 同じ音声なら停止
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current.currentTime = 0;
      }
      setPlayingAudio(null);
    } else {
      // 別の音声または初回再生
      if (audioRef.current) {
        audioRef.current.pause();
      }
      
      audioRef.current = new Audio(assetUrl);
      audioRef.current.volume = 0.5;
      audioRef.current.onended = () => setPlayingAudio(null);
      
      audioRef.current.play()
        .then(() => setPlayingAudio(assetUrl))
        .catch(err => {
          console.error('音声再生エラー:', err);
          setPlayingAudio(null);
        });
    }
  };
  
  // ファイル名変更ダイアログを表示
  const showRenameDialog = (category, subCategory, fileName, fileData) => {
    setSelectedAsset({
      category,
      subCategory,
      fileName,
      fileData
    });
    setNewFileName(fileName);
    setShowRenameModal(true);
  };
  
  // ファイル名変更を実行
  const handleRename = () => {
    if (!selectedAsset || !newFileName.trim()) return;
    
    const { category, subCategory, fileName, fileData } = selectedAsset;
    
    // 同じファイル名なら変更なし
    if (fileName === newFileName) {
      setShowRenameModal(false);
      return;
    }
    
    // 拡張子を維持
    const oldExt = fileName.split('.').pop();
    const newFileNameWithExt = newFileName.includes('.') 
      ? newFileName 
      : `${newFileName}.${oldExt}`;
    
    // 同名ファイルの存在確認
    if (assets[category][subCategory][newFileNameWithExt]) {
      alert('同じ名前のファイルが既に存在します');
      return;
    }
    
    // 新しいアセット一覧を作成
    const newAssets = { ...assets };
    
    // 古いエントリを削除
    delete newAssets[category][subCategory][fileName];
    
    // 新しいエントリを追加
    newAssets[category][subCategory][newFileNameWithExt] = { ...fileData };
    
    setAssets(newAssets);
    setShowRenameModal(false);
  };
  
  // アセットを削除
  const deleteAsset = (category, subCategory, fileName) => {
    if (window.confirm(`${fileName}を削除してもよろしいですか？`)) {
      const newAssets = { ...assets };
      
      // BlobURLのリソースを解放
      URL.revokeObjectURL(newAssets[category][subCategory][fileName].url);
      
      // エントリを削除
      delete newAssets[category][subCategory][fileName];
      
      setAssets(newAssets);
    }
  };
  
  // アセット一覧を表示
  const renderAssetList = () => {
    return (
      <div className="mt-4">
        <h3 className="font-bold mb-2">登録済みアセット</h3>
        
        {Object.entries(assets).map(([category, subCategories]) => (
          <div key={category} className="mb-4">
            <h4 className="font-semibold text-blue-600">{category}</h4>
            
            {Object.entries(subCategories).map(([subCategory, files]) => {
              // ファイルがない場合はスキップ
              if (Object.keys(files).length === 0) return null;
              
              return (
                <div key={subCategory} className="ml-4 mb-2">
                  <h5 className="font-medium text-gray-700">{subCategory}</h5>
                  
                  <div className="ml-4">
                    {Object.entries(files).map(([fileName, fileData]) => (
                      <div key={fileName} className="flex items-center py-1 border-b">
                        <div className="flex-1 text-sm truncate">
                          {fileName} ({Math.round(fileData.size / 1024)} KB)
                        </div>
                        
                        <div className="flex space-x-2">
                          {/* 音声プレビュー */}
                          {fileData.type.startsWith('audio/') && (
                            <button
                              className={`p-1 ${playingAudio === fileData.url ? 'text-red-500' : 'text-green-500'}`}
                              onClick={() => toggleAudioPreview(fileData.url)}
                              title={playingAudio === fileData.url ? '停止' : '再生'}
                            >
                              {playingAudio === fileData.url ? <Square size={16} /> : <Play size={16} />}
                            </button>
                          )}
                          
                          {/* 画像プレビュー */}
                          {fileData.type.startsWith('image/') && (
                            <div className="w-8 h-8 bg-contain bg-center bg-no-repeat border">
                              <img src={fileData.url} alt={fileName} className="w-full h-full object-contain" />
                            </div>
                          )}
                          
                          {/* ファイル名変更ボタン */}
                          <button
                            className="p-1 text-blue-500 hover:text-blue-700"
                            onClick={() => showRenameDialog(category, subCategory, fileName, fileData)}
                            title="名前変更"
                          >
                            <Edit size={16} />
                          </button>
                          
                          {/* 削除ボタン */}
                          <button
                            className="p-1 text-red-500 hover:text-red-700"
                            onClick={() => deleteAsset(category, subCategory, fileName)}
                            title="削除"
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              );
            })}
          </div>
        ))}
      </div>
    );
  };
  
  return (
    <div className="bg-white rounded-lg shadow p-4">
      <h2 className="text-lg font-bold mb-4">アセット管理</h2>
      
      <div className="mb-6">
        <h3 className="font-bold mb-2">アセットフォルダを読み込む</h3>
        <p className="text-sm text-gray-600 mb-2">
          以下の構造のフォルダを選択してください：<br />
          asset/image/bg - 背景画像<br />
          asset/image/character - キャラクター画像<br />
          asset/sound/bgm - BGM音楽<br />
          asset/sound/se - 効果音
        </p>
        
        <label className="inline-block px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 cursor-pointer">
          <FolderOpen size={16} className="inline-block mr-2" /> asset フォルダを選択
          <input 
            ref={folderInputRef}
            type="file" 
            className="hidden" 
            webkitdirectory="true" 
            directory="true" 
            onChange={handleFolderSelect}
          />
        </label>
        
        <p className="text-xs text-gray-500 mt-2">
          ※ フォルダ選択がうまく動作しない場合は、個別アセット追加を使用してください。
        </p>
      </div>
      
      <div className="grid grid-cols-2 gap-4">
        <div>
          <h3 className="font-bold mb-2">個別アセット追加</h3>
          <div className="flex flex-wrap gap-2">
            <label className="px-3 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 cursor-pointer flex items-center">
              <ImageIcon size={16} className="mr-1" /> 背景画像
              <input 
                type="file" 
                className="hidden" 
                accept="image/*" 
                multiple
                onChange={(e) => {
                  Array.from(e.target.files).forEach(file => {
                    const blobUrl = URL.createObjectURL(file);
                    
                    const newAssets = { ...assets };
                    if (!newAssets.image) newAssets.image = {};
                    if (!newAssets.image.bg) newAssets.image.bg = {};
                    
                    newAssets.image.bg[file.name] = {
                      url: blobUrl,
                      type: file.type,
                      size: file.size,
                      lastModified: file.lastModified
                    };
                    
                    setAssets(newAssets);
                  });
                }}
              />
            </label>
            
            <label className="px-3 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 cursor-pointer flex items-center">
              <ImageIcon size={16} className="mr-1" /> キャラクター画像
              <input 
                type="file" 
                className="hidden" 
                accept="image/*" 
                multiple
                onChange={(e) => {
                  Array.from(e.target.files).forEach(file => {
                    const blobUrl = URL.createObjectURL(file);
                    
                    const newAssets = { ...assets };
                    if (!newAssets.image) newAssets.image = {};
                    if (!newAssets.image.character) newAssets.image.character = {};
                    
                    newAssets.image.character[file.name] = {
                      url: blobUrl,
                      type: file.type,
                      size: file.size,
                      lastModified: file.lastModified
                    };
                    
                    setAssets(newAssets);
                  });
                }}
              />
            </label>
          </div>
        </div>
        
        <div>
          <h3 className="font-bold mb-2">音声</h3>
          <div className="flex flex-wrap gap-2">
            <label className="px-3 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 cursor-pointer flex items-center">
              <Music size={16} className="mr-1" /> BGM
              <input 
                type="file" 
                className="hidden" 
                accept="audio/*" 
                multiple
                onChange={(e) => {
                  Array.from(e.target.files).forEach(file => {
                    const blobUrl = URL.createObjectURL(file);
                    
                    const newAssets = { ...assets };
                    if (!newAssets.sound) newAssets.sound = {};
                    if (!newAssets.sound.bgm) newAssets.sound.bgm = {};
                    
                    newAssets.sound.bgm[file.name] = {
                      url: blobUrl,
                      type: file.type,
                      size: file.size,
                      lastModified: file.lastModified
                    };
                    
                    setAssets(newAssets);
                  });
                }}
              />
            </label>
            
            <label className="px-3 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 cursor-pointer flex items-center">
              <Music size={16} className="mr-1" /> 効果音
              <input 
                type="file" 
                className="hidden" 
                accept="audio/*" 
                multiple
                onChange={(e) => {
                  Array.from(e.target.files).forEach(file => {
                    const blobUrl = URL.createObjectURL(file);
                    
                    const newAssets = { ...assets };
                    if (!newAssets.sound) newAssets.sound = {};
                    if (!newAssets.sound.se) newAssets.sound.se = {};
                    
                    newAssets.sound.se[file.name] = {
                      url: blobUrl,
                      type: file.type,
                      size: file.size,
                      lastModified: file.lastModified
                    };
                    
                    setAssets(newAssets);
                  });
                }}
              />
            </label>
          </div>
        </div>
      </div>
      
      {Object.keys(assets).length > 0 && renderAssetList()}
      
      <div className="mt-4 pt-4 border-t flex justify-end">
        <button 
          className="px-3 py-2 bg-green-500 text-white rounded hover:bg-green-600 flex items-center"
          onClick={() => {
            // アセット一覧をローカルストレージに保存
            localStorage.setItem('novel_maker_assets', JSON.stringify(assets));
            alert('アセット情報を保存しました');
          }}
        >
          <Save size={16} className="mr-1" /> アセット情報を保存
        </button>
      </div>
      
      {/* ファイル名変更モーダル */}
      {showRenameModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 w-96">
            <h2 className="text-xl font-bold mb-4">ファイル名変更</h2>
            
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">
                新しいファイル名
              </label>
              <input 
                type="text"
                value={newFileName}
                onChange={(e) => setNewFileName(e.target.value)}
                className="w-full p-2 border rounded"
                autoFocus
              />
            </div>
            
            <div className="flex justify-end space-x-2">
              <button 
                className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400"
                onClick={() => setShowRenameModal(false)}
              >
                キャンセル
              </button>
              <button 
                className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
                onClick={handleRename}
              >
                変更
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default AssetManager;